import {Component, OnInit, Input} from '@angular/core';
import {ImputationCube} from '../../../model';
import {nest, sum} from 'd3';

@Component({
    selector: 'app-chart-cdt',
    templateUrl: './chart-cdt.component.html',
    styleUrls: ['./chart-cdt.component.scss']
})
export class ChartCDTComponent implements OnInit {

    _datas: Array<ImputationCube> = [];
    itemSelected: string;

    @Input() set datas(values: Array<ImputationCube>) {
        this._datas = values;
        this.refreshData();
    }

    dataFiltered: any;

    constructor() {}

    ngOnInit() {
        this.refreshData();
    }

    refreshData() {
        var self = this;
        var datasWithFilter = this._datas.filter(function (d: ImputationCube) {return d.activite === "CDT"});
        var self = this;

        if (this.itemSelected) {
            datasWithFilter = datasWithFilter.filter(function (d: ImputationCube) {return d.categorieNom === self.itemSelected});
        }

        this.dataFiltered = nest()
            .key(function (d: ImputationCube) {
                if (self.itemSelected) {
                    return d.sousCategorieNom
                }
                return d.categorieNom
            })
            .rollup(function (v) {return sum(v, function (d: ImputationCube) {return d.charge}) as any})
            .entries(datasWithFilter)
            .map(function (group) {
                return {
                    name: group.key,
                    value: group.value
                };
            });

    }

    filter(value: string = null) {
        if (!this.itemSelected || !value) {
            this.itemSelected = value;
            this.refreshData();
        }
        return false;
    }

}



