import {Component, OnInit, Input} from '@angular/core';
import {Activite} from '../../../model';
import {Router} from '@angular/router';
import * as moment from 'moment';

@Component({
    selector: 'app-chart-activite',
    templateUrl: './chart-activite.component.html',
    styleUrls: ['./chart-activite.component.scss']
})
export class ChartActiviteComponent implements OnInit {

    _datas: Array<Activite> = [];
    customColors = new Array();
    dataFiltered: any= [];

    @Input() set datas(values: Array<Activite>) {
        this.dataFiltered = [];
        this._datas = values;
        this.refreshData();
    }

    @Input() fromDate: moment.Moment;


    constructor(private router: Router) {}

    ngOnInit() {
        this.refreshData();
    }

    refreshData() {
        if (this._datas) {
            let activites = new Array();
            let semaine = this.fromDate.isoWeeks();
            let currentSemaine = moment().isoWeeks();
            let colors = new Array();
            for (let i = semaine; i <= currentSemaine; i++) {
                let name = "S" + i;
                let activite = this._datas.find((d: Activite) => d.semaine == i);
                if (activite) {
                    let total: number = 0;
                    for (let ligne of activite.imputations) {
                        for (let charge of ligne.charges) {
                            total += charge.value || 0;
                        }
                    }
                    let totalPct:Number = total / 5;
                    colors.push({
                        name: name,
                        value: totalPct < 80 ? '#f46969' : totalPct < 100 ? '#f2a876' : '#94e8a9'
                    });
                    activites.push({
                        name: name,
                        value: totalPct
                    });
                } else {
                    colors.push({
                        name: name,
                        value: '#f46969'
                    });
                    activites.push({name: name, value: 0});
                }
            }
            this.customColors = colors;
            this.dataFiltered = activites;
        }

    }

    goTo(event: any = null) {
        let name:string = event.name || event;
        if(name){
            let date = moment().isoWeeks(Number(name.replace("S",""))).startOf('isoWeek').toISOString();
            if (date) {
                this.router.navigate(['/activites/' + date]);
            }
        }
    }

}



