
import {Constant} from './constants';

export class DBEntity {
    _id: string;
    _rev: string;
    auteurId: string;
    reviseurId: string;
    revDate: Date;
    isDeleted: boolean = false;
}


export enum Role {
    Administrateur = 'Administrateur', Collaborateur = 'Collaborateur'
}

export class Collaborateur extends DBEntity{
    type: string = "Collaborateur";
    civilite: string;
    nom: string;
    prenom: string;
    mail: string;
    telephone: string;
    fonction: string;
    enCasUrgence: string;
    adresse: Adresse = new Adresse;
    role: Role = Role.Collaborateur;
}

export class Contact extends DBEntity{
    type: string = "Contact";
    civilite: string;
    nom: string;
    prenom: string;
    mail: string;
    telephone: string;
    relationPrivilegiee: boolean;
    commentaire: string;
    dateRencontre: string;
    liensOrganisme: Array<LienOrganisme> = new Array<LienOrganisme>();
}
export class LienOrganisme {
    mail: string;
    telephone: string;
    organisme: Organisme = new Organisme();
    fonction: string;
}


export class Location {
    latitude: number;
    longitude: number;
}

export class Adresse {
    numero: string;
    rue: string;
    complement: string;
    residence: string;
    boitePostale: string;
    codePostal: string;
    ville: string;
    cedex: string;
    location: Location;
}

export class Organisme extends DBEntity{
    type: string = "Organisme";
    typeOrganisme: string;
    nom: string;
    adresse: Adresse = new Adresse;
    telephone: string;
    web: string;
}


export class Acknowledge {
    ok: string;
    id: string;
    rev: string;
}

export class Item {
    nom: string;
    numero: string;
    commentaire: string;
    sousItems: Array<Item>;
}


export class Parametres extends DBEntity{
    _id: string = "Parametres";
    listesDeChoix: {
        cible: Array<string>;
        typeOrganisme: Array<string>;
        thematique: Array<string>;
        modaliteIS: Array<string>;
        natureRelation: Array<string>;
        typeFinanceurTiers: Array<string>;
        modaliteObtention: Array<string>;
        missionRole: Array<string>;
        secteurs: Array<string>;
        missionFormat: Array<string>;
        missionType: Array<string>;
        modaliteOffre: Array<string>;
        agrement: Array<string>;
        civilite: Array<string>;
        prospectionStatut: Array<string>;
        action: Array<string>;
        activite: Array<string>;
        programme: Array<string>;
        noteType: Array<string>;
    }
    workpackages: Array<Item>;
    cdt: Array<Item>;


    constructor() {
        super();
        this.listesDeChoix = {
            cible: new Array<string>(),
            typeOrganisme: new Array<string>(),
            thematique: new Array<string>(),
            modaliteIS: new Array<string>(),
            natureRelation: new Array<string>(),
            typeFinanceurTiers: new Array<string>(),
            modaliteObtention: new Array<string>(),
            secteurs: new Array<string>(),
            missionRole: new Array<string>(),
            missionFormat: new Array<string>(),
            missionType: new Array<string>(),
            modaliteOffre: new Array<string>(),
            agrement: new Array<string>(),
            civilite: new Array<string>(),
            prospectionStatut: new Array<string>(),
            action: new Array<string>(),
            activite: new Array<string>(),
            programme: new Array<string>(),
            noteType: new Array<string>()
        };
    }
}

export class MissionSynthese {
    resume: string;
    besoinsIdentifies: string;
    objectifs: string;
    enjeu: string;
    methode: string;
    contraintes: string;
    pistesValorisation: string;
    miseEnRelation: string;
    evolution: string;
}

export class MissionAvancement {
    dateDebut: Date;
    dateFin: Date;
    avancement: number;

    constructor(avancement: number = 0, dateDebut: Date = new Date(), dateFin: Date = null) {
        this.dateDebut = dateDebut;
        this.avancement = avancement;
        this.dateFin = dateFin;
    }
}

export class Mission extends DBEntity{
    nom: string;
    type: string = "Mission";
    numFacture: string;
    contact: Contact;
    client: Organisme;
    financeur: Organisme;
    cdt: boolean;
    programme: string;
    dateDebut: string;
    dateFin: string;
    nbJours: number;
    missionType: string;
    format: string;
    dateCloture: Date;
    isClosed: boolean = false;
    modaliteObtention: string;
    montantProposition: string;
    cible: string;
    thematique: string;
    modaliteIS: string;
    commentaire: string;
    dropboxRepertoire: string;
    restrictionAcces: Array<Collaborateur> = new Array<Collaborateur>();
    liensCollaborateur: Array<LienMissionCollaborateur> = new Array<LienMissionCollaborateur>();
    liensMission: Array<LienEntreMission> = new Array<LienEntreMission>();
    avancement: number = 0;
    historiqueAvancement: Array<MissionAvancement>;
    prospectionId: string;
    synthese: MissionSynthese = new MissionSynthese();
    
    
    addCP(collaborateur:Collaborateur){
        let cp = new LienMissionCollaborateur();
        cp.role = Constant.MISSION_ROLE_CP;
        cp.collaborateur = new Collaborateur();
        cp.collaborateur._id = collaborateur._id;
        cp.collaborateur.nom = collaborateur.nom;
        cp.collaborateur.prenom = collaborateur.prenom;
        this.liensCollaborateur = [cp];
    }
}

export class LienMissionCollaborateur {
    role: string;
    collaborateur: Collaborateur = new Collaborateur();
}
export class LienEntreMission {
    typeLien: string;
    commentaire: string;
    mission: Mission = new Mission();
}

export class Prospection extends DBEntity{
    type: string = "Prospection";
    nom: string;
    numProspection: string;
    contact: Contact;
    client: Organisme;
    dateContact: string;
    dateCloture: string;
    modaliteObtention: string
    cible: string;
    thematique: string;
    modaliteIS: string;
    commentaire: string;
    montantProposition: string;
    nbJours: string;
    modaliteOffre: string;
    dropboxRepertoire: string;
    statut: string;
    dateChangementStatut: Date;
}

export class Note extends DBEntity{
    type: string = "Note";
    noteType: string;
    auteur: Collaborateur = new Collaborateur();
    collaborateursConcernes: Array<Collaborateur> = new Array<Collaborateur>();
    date: string;
    adresse: Adresse;
    lieu: string;
    objetDeclencheur: string;
    faitObserve: string;
    commentaire: string;
    impactActivite: string;
    contact: Contact;
    organisme: Organisme;
}

export enum ActionStatut {
    TODO = 'TODO',
    DONE = 'DONE'
}

export class Action extends DBEntity{
    type: string = "Action";
    statut: ActionStatut = ActionStatut.TODO;
    entiteLienType: string;
    entiteLienId: string;
    entiteLienNom: string;
    date: Date;
    typeAction: string;
    collaborateur: Collaborateur = new Collaborateur();
    notificationDate: string;
    notificationType: string;
}

export class Charge {
    value: number = 0;
}

export class ImputationLienEntite {
    _id: string;
    type: string;
    nom: string;
    nomCourt: string;
    
    constructor(_id:string, type:string, nom:string, nomCourt:string){
        this._id = _id;
        this.type = type;
        this.nom = nom;
        this.nomCourt = nomCourt;
    }
}

export class Imputation {
    activite: string;
    label: string;
    sousImputations: Array<Imputation> = new Array<Imputation>();
    description: string;
    charges: Array<Charge> = [{value: 0}, {value: 0}, {value: 0}, {value: 0}, {value: 0}, {value: 0}, {value: 0}];
    mission: ImputationLienEntite;
    contact: ImputationLienEntite;
    organisme: ImputationLienEntite;
    categorieId: string;
    categorieNom: string;
    sousCategorieId: string;
    sousCategorieNom: string;
}


export class Activite extends DBEntity{
    type: string = "Activite";
    annee: number;
    semaine: number;
    date: string;
    collaborateur: Collaborateur = new Collaborateur();
    imputations: Array<Imputation> = new Array<Imputation>();
    isComplete: boolean;
}

export class ImputationCube {
    _id: string;
    annee: number;
    semaine: number;
    jour: number;
    mois: number;
    date: string;
    collaborateurId: string;
    collaborateurNom: string;
    activite: string;
    label: string;
    categorieId: string;
    categorieNom: string;
    sousCategorieId: string;
    sousCategorieNom: string;
    description:string;
    charge: number = 0;
    mission: ImputationLienEntite;
    contact: ImputationLienEntite;
    organisme: ImputationLienEntite;
}

export class DocumentSyncEvent {
    ids: Array<string>;
    type: string;
}

export class PouchDbEvent {
    connected: boolean;
    message: string;
}

export class Tache extends DBEntity{
    type: string = "Tache";
    collaborateurId: string;
    commentaire: string;
}

export class Photo {
    image: string;

    getImageWithoutContentType() {
        if (this.image) {
            return this.image.replace("data:" + this.getContentType() + ";base64,", "");
        }
    }

    getContentType() {
        if (this.image) {
            return this.image.substring(0, this.image.indexOf("base64") + 7).replace("data:", "").replace(";base64,", "");
        }
    }
}


