import {Component, OnInit, ViewChild} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {FormGroup, FormBuilder, Validators} from '@angular/forms';
import {Collaborateur, Location, Adresse, Role, Photo} from '../../model';
import {ActivatedRoute} from "@angular/router";
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Router} from '@angular/router';
import {} from 'googlemaps';
import {GooglePlaceService} from '../../services/google-place-service/google-place.service';
import {merge} from 'micro-dash';
import * as alertify from 'alertifyjs/build/alertify.js';
import {AuthService} from './../../services/auth/auth.service';
import {CropperSettings} from "ngx-img-cropper";

@Component({
    selector: 'app-collaborateur',
    templateUrl: './collaborateur.component.html',
    styleUrls: ['./collaborateur.component.scss']
})
export class CollaborateurComponent implements OnInit {
    form: FormGroup;
    private formSubmitAttempt: boolean = false;
    errorMessage: String;
    editForm = false;
    createForm = false;
    collaborateur: Collaborateur = new Collaborateur();
    civilites: Array<string> = [];
    roles: Array<string> = Object.keys(Role).filter(f => isNaN(Number(f)));
    private place: google.maps.places.PlaceResult
    isAdmin$: Observable<boolean>;
    isMyProfile: boolean = false;
    cropperSettings: CropperSettings;
    photo: Photo = new Photo();


    constructor(private fb: FormBuilder, private route: ActivatedRoute,
        private pouchdbservice: PouchdbService,
        private router: Router,
        private googlePlaceService: GooglePlaceService,
        private authService: AuthService
    ) {
        this.isAdmin$ = this.authService.isAdmin;
        this.form = this.fb.group({
            nom: ['', Validators.required],
            prenom: ['', Validators.required],
            civilite: [''],
            telephone: [''],
            mail: ['', Validators.required],
            role: [''],
            fonction: [''],
            enCasUrgence: [''],
            adresse: this.fb.group({
                adresseComplete: '',
                complement: '',
                rue: '',
                numero: '',
                residence: '',
                ville: '',
                codePostal: '',
            }),
        });

        this.cropperSettings = new CropperSettings();
        this.cropperSettings.width = 200;
        this.cropperSettings.height = 200;
        this.cropperSettings.canvasWidth = 200;
        this.cropperSettings.canvasHeight = 200;
        this.cropperSettings.croppedWidth = 200;
        this.cropperSettings.croppedHeight = 200;
        this.cropperSettings.rounded = true;
    }

    ngOnInit() {
        this.route.params.subscribe(
            params => {
                var id = params['id'];
                if (id == '0') {
                    this.editForm = false;
                    this.createForm = true;
                } else {
                    this.loadData(id);
                }
            });
        this.pouchdbservice.getParametres().subscribe(
            data => {
                this.civilites = data && data.listesDeChoix && data.listesDeChoix.civilite;
            },
            err => {
                this.errorMessage = err;
            }
        );

    }

    loadData(id: string) {
        this.pouchdbservice.get(id).subscribe(
            response => {
                this.collaborateur = response;
                this.authService.collaborateurObs.subscribe(
                    collaborateur => {
                        if (collaborateur) {
                            this.isMyProfile = collaborateur._id === this.collaborateur._id;
                        }
                    },
                    err => {
                        this.errorMessage = err;
                    }
                );
            },
            err => {
                this.errorMessage = err;
            }
        );

    }

    toggleEdit() {
        this.editForm = !this.editForm;
        if (this.editForm) {
            this.form.patchValue(this.collaborateur);
        }
    }

    isFieldInvalid(field: string) {
        return (
            this.form.get(field).invalid &&
            (this.form.get(field).touched || (this.form.get(field).untouched && this.formSubmitAttempt))
        );
    }

    setAddressOnChange(place: google.maps.places.PlaceResult) {
        this.place = place;
        if (this.place) {
            //set latitude, longitude and zoom
            var adresse: Adresse = new Adresse();
            adresse.location = new Location();
            adresse.location.latitude = this.googlePlaceService.latitude(this.place);
            adresse.location.longitude = this.googlePlaceService.longitude(this.place);
            adresse.numero = this.googlePlaceService.street_number(this.place);
            adresse.rue = this.googlePlaceService.street(this.place);
            adresse.codePostal = this.googlePlaceService.postal_code(this.place);
            adresse.ville = this.googlePlaceService.city(this.place);
            adresse.residence = this.googlePlaceService.establishment(this.place);
            adresse.complement = this.googlePlaceService.floor(this.place);
            adresse.ville = this.googlePlaceService.city(this.place);
            adresse['adresseComplete'] = undefined;
            this.form.get('adresse').patchValue(adresse);
        } else {
            this.form.get('adresse').get('location').setValue(new Location());
        }
    }

    onSubmit() {
        this.errorMessage = "";
        if (this.form.valid) {
            const oldEmail = this.collaborateur.mail;
            let newEmail = this.form.get('mail').value as string;
            if (!this.collaborateur._id || oldEmail !== newEmail) {
                this.pouchdbservice.getAllCollaborateurs().subscribe((collabos) => {
                    let collabo = collabos.find(item => item._id != this.collaborateur._id && item.mail.trim() === newEmail.trim())
                    if (collabo){
                        this.errorMessage = "Un utilisateur avec cet email existe dj : " + collabo.nom + " " + collabo.prenom;
                    } else {
                        this.save();
                    }
                });
            } else {
                this.save();
            }
        } else {
            this.errorMessage = "Tous les champs obligatoires ne sont pas renseigns";
        }
        this.formSubmitAttempt = true;
    }


    save() {
        this.collaborateur = merge(this.collaborateur, this.form.value);
        if (!this.collaborateur.role){
            this.collaborateur.role = Role.Collaborateur;
        }
        this.pouchdbservice.save(this.collaborateur).subscribe(
            response => {
                console.log(response);
                this.addImage(response.id, response.rev);
            },
            err => {
                this.errorMessage = err;
            }
        );
    }

    addImage(id: string, rev: string) {
        if (this.photo.image) {
            let self = this;
            this.pouchdbservice.addImage(id, rev, this.photo.getContentType(), this.photo.getImageWithoutContentType()).subscribe(
                response => {
                    self.swithToShowMode(id)
                },
                err => {
                    self.errorMessage = err;
                }
            );
        } else {
            this.swithToShowMode(id)
        }
    }

    swithToShowMode(id) {
        if (this.createForm) {
            this.createForm = false;
            this.editForm = false;
            this.router.navigate(['/collaborateurs/' + id]);
        } else {
            this.createForm = false;
            this.editForm = false;
            this.loadData(id);
        };
    }

    undeleteCollaborateur() {
        let self = this;
        alertify.confirm('Etes-vous sr de vouloir ractiver cet collaborateur', function () {
            self.collaborateur.isDeleted = false;
            self.saveCollaborateur();
        });
    }

    deleteCollaborateur() {
        let self = this;
        alertify.confirm('Etes-vous sr de vouloir supprimer cet collaborateur', function () {
            self.collaborateur.isDeleted = true;
            self.saveCollaborateur();
        });
    }

    saveCollaborateur() {
        this.pouchdbservice.save(this.collaborateur).subscribe(
            response => {
                this.swithToShowMode(this.collaborateur._id)
            },
            err => {
                this.errorMessage = err;
            }
        );
    }

}



