import {Component, OnInit, Input} from '@angular/core';
import {ImputationCube, Mission} from '../../../model';
import {nest, sum} from 'd3';
import {PouchdbService} from '../../../services/pouchdb-service/pouchdb.service';

@Component({
  selector: 'app-chart-missions',
  templateUrl: './chart-missions.component.html',
  styleUrls: ['./chart-missions.component.scss']
})
export class ChartMissionsComponent implements OnInit {

 _datas: Array<ImputationCube> = [];
 mission:Mission;

    @Input() set datas(values: Array<ImputationCube>) {
        this._datas = values;
        this.refreshData();
    }

    dataFiltered: any;

    constructor(private pouchdbservice: PouchdbService) {}

    ngOnInit() {
        this.refreshData();
    }

    refreshData() {
        var self = this;
        var datasWithFilter = this._datas.filter(function (d: ImputationCube) {return d.activite==="Mission"});
        
        this.dataFiltered = nest()
            .key(function (d: ImputationCube) {return d.label})
            .rollup(function (v) {return sum(v, function (d: ImputationCube) {return d.charge}) as any})
            .entries(datasWithFilter)
            .map(function (group) {
                return {
                    name: group.key,
                    value: group.value
                };
            });

    }
    
     displayMission(value:string = null) {
        if(value){
            this.pouchdbservice.getMissionFromNumFacture(value).subscribe(
            data => {
                this.mission = data;
            },
            err => {
                console.log(err);
            }
        );
        }
        return false;
    }

}



