import {Directive, ChangeDetectorRef, AfterViewChecked, TemplateRef, ViewContainerRef, Input} from '@angular/core';

@Directive({selector: '[ngxResponsiveColumn]'})
export class NgxResponsiveColumnDirective implements AfterViewChecked {

    private _visible = false;

    @Input('ngxResponsiveColumn') responsive: boolean;

    constructor(private _templateRef: TemplateRef<any>,
        private _viewContainer: ViewContainerRef,
        private cdRef: ChangeDetectorRef) {

    }

    ngAfterViewChecked() {
        if (window.innerWidth < 768 && this.responsive) {
            this.hide();
        } else {
            this.show();
        }

    }

    private show() {
        if (this._visible) {
            return;
        }
        this._visible = true;
        setTimeout(() => {
            this._viewContainer.clear();
            this._viewContainer.createEmbeddedView(this._templateRef);
        });
    }

    private hide() {
        if (!this._visible) {
            return;
        }
        this._visible = false;
        setTimeout(() => {
            this._viewContainer.clear();
        });
    }

}


