import {Injectable} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {Subject} from 'rxjs/Subject';
import {Router} from '@angular/router';
import {BehaviorSubject, } from 'rxjs/BehaviorSubject';
import {User} from './user';
import {Collaborateur, PouchDbEvent, Role} from '../../model';
import {environment} from '../../../environments/environment';
import {HttpClient} from '@angular/common/http';
import {EventService} from '../../services/event-service/event.service';
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';

@Injectable()
export class AuthService {
    private loggedIn: BehaviorSubject<boolean> = new BehaviorSubject(false);
    private _user: User;
    private _collaborateur: BehaviorSubject<Collaborateur> = new BehaviorSubject(null);
    private admin: BehaviorSubject<boolean> = new BehaviorSubject(false);

    private REGISTRATION_SERVICE: string = 'registration/'
    private FORGOTPASSWORD_SERVICE: string = 'registration/{login}/resetPassword'

    get isLoggedIn() {
        return this.loggedIn.asObservable();
    }

    get user() {
        return this._user;
    }

    get collaborateurObs() {
        return this._collaborateur.asObservable();
    }

    get isAdmin() {
        return this.admin.asObservable();
    }


    constructor(private eventService: EventService, private pouchdbService: PouchdbService, private router: Router, private http: HttpClient) {
        this.eventService.getPouchDbEmitter().subscribe((item: PouchDbEvent) => {
            if (item.connected) {
                this.loadCurrentUser();
            } else {
                this._collaborateur.next(null);
                this.loggedIn.next(false);
                this.router.navigateByUrl('/login?err=' + item.message);
            }
            //            this.eventService.getPouchDbEmitter().unsubscribe();
        });

        let _login: string = localStorage.getItem('login');
        let _password: any = localStorage.getItem('password');
        try {
            _password = window.atob(_password);
        } catch (error) {
            this._collaborateur.next(null);
            this.loggedIn.next(false);
            this.router.navigateByUrl('/login');
            console.log("Impossible de lire le mot de passe");
            return;
        }
        if (!!_login && !!_password) {
            this.loggedIn.next(true);
            this._user = new User(_login, _password);
            this.pouchdbService.initConnexion(_login, _password);
            // if offline, considere that we are logged
            this.pouchdbService.couchdbUp.subscribe(state => {
                if (!state) {
                    this.loadCurrentUser();
                }
            })
        } else {
            this._collaborateur.next(null);
            this.loggedIn.next(false);
            this.router.navigateByUrl('/login');
            return;
        }

    }

    login(user: User) {
        if (!window.navigator.onLine) {
            this.loggedIn.next(false);
            this.router.navigateByUrl('/login?err=' + encodeURI('Vous ne pouvez pas vous connecter en offline'));
        } else {
            if (user.login !== '' && user.password != '') {
                localStorage.setItem('login', user.login);
                localStorage.setItem('password', window.btoa(user.password));
                this._user = user;
                this.pouchdbService.initConnexion(user.login, user.password);
            }
        }

    }

    loadCurrentUser() {
        this.pouchdbService.getCollaborateurByLogin(this._user.login).subscribe(collaborateur => {
            if (collaborateur) {
                this._collaborateur.next(collaborateur);
                let isAdmin = collaborateur != null && collaborateur.role === Role.Administrateur;
                this.admin.next(isAdmin);
                this.loggedIn.next(true);
                this.pouchdbService.setCurrentCollaborateur(collaborateur);
                if (this.router.url.indexOf("login") >= 0) {
                    this.router.navigate(['/']);
                }
            }
        })
    }

    logout() {
        this.pouchdbService.closeConnexion();
        localStorage.clear();
        this._user = null;
        this.loggedIn.next(false);
        this.router.navigateByUrl('/login');
    }

    forgotPassword(login: string): Observable<any> {
        return this.http.get(environment.backend_url + this.FORGOTPASSWORD_SERVICE.replace("{login}", encodeURIComponent(login)));
    }

    registerUser(email: string): Observable<any> {
        return this.http.post(environment.backend_url + this.REGISTRATION_SERVICE, {email: email});
    }
}



