import * as moment from 'moment';
import {Angular5Csv} from 'angular5-csv/Angular5-csv';

export function sanitize(str: string) {
    if (!str) return '';
    return str.normalize('NFD').replace(/[\u0300-\u036f]/g, "").toLowerCase();
}

const DATE_FORMAT = "YYYY-MM-DD";

export function formatDateInString(d: Date): string {
    return moment(d).format(DATE_FORMAT);
}
export function formatMomentInString(m: moment.Moment): string {
    return m.format(DATE_FORMAT);
}
export function parseStringToDate(d: string): Date {
    return moment(d).toDate();
}
export function getActiviteDateOfWeek(m: moment.Moment): string {
    return m.startOf('isoWeek').format(DATE_FORMAT);
}

export function exportRowsToCsv(headerKeys:Array<string>, rows:Array<object>, filename:string) {
    var defaultsObj:any = {};
    headerKeys.forEach(key => defaultsObj[key] = "");
    
    let datas = rows.map(item => {
        var newObj:any = {};
        headerKeys.forEach(key => {
            newObj[key] = item[key] || "";
        });
        return newObj;
    });
    var options = {
        fieldSeparator: ';',
        showLabels: true,
        headers: datas ? Object.keys(datas[0]) : "-"
    };
    new Angular5Csv(datas, filename, options);
}






