import {Injectable} from '@angular/core';
import {Note, Mission, Adresse, Contact, Organisme, Prospection} from '../../model';

import * as moment from 'moment';
import * as pdfMake from 'pdfmake/build/pdfmake.js';
import * as pdfFonts from 'pdfmake/build/vfs_fonts.js';
pdfMake.vfs = pdfFonts.pdfMake.vfs;

@Injectable()

export class PdfGeneratorService {

    constructor() {}


    generateContactPdfAndOpen(contact: Contact) {
        let title = contact.nom + " " + contact.prenom;

        let organismesList: Array<any> = [];
        for (let lien of contact.liensOrganisme) {
            organismesList.push({text: [{text: lien.organisme.nom + " : ", bold: true}, lien.fonction + ", Email : " + lien.mail + ", Tlphone : " + lien.telephone]});
        }

        let content = [
            {text: title, style: "title1"},
            {
                columns: [
                    {
                        style: "list",
                        width: '30%',
                        bold: true,
                        text: [
                            "Date rencontre\n",
                            "Commentaires"
                        ]

                    },
                    {
                        style: "list",
                        width: 'auto',
                        text: [
                            this.formatDateToDisplay(contact.dateRencontre) + "\n",
                            contact.commentaire + "\n"
                        ]
                    }
                ]
            },
            {text: "Coordonnes", style: "title2"},
            {
                columns: [
                    {
                        style: "list",
                        width: '30%',
                        bold: true,
                        text: [
                            "Tlphone\n",
                            "Email",
                        ]

                    },
                    {
                        style: "list",
                        width: 'auto',
                        text: [
                            contact.telephone + "\n",
                            contact.mail + "\n",
                        ]
                    }
                ]
            },
            {text: "Organismes", style: "title2"},
            {
                ul: organismesList
            },

        ];
        this.generatePdfAndOpen(title, "Fiche " + "Contact", content);

    }

    generateOrganismePdfAndOpen(organisme: Organisme) {
        let title = organisme.nom;


        let content = [
            {text: title, style: "title1"},
            {
                columns: [
                    {
                        style: "list",
                        width: '30%',
                        bold: true,
                        text: [
                            "Type d'organisme\n"
                        ]

                    },
                    {
                        style: "list",
                        width: 'auto',
                        text: [
                            organisme.typeOrganisme
                        ]
                    }
                ]
            },
            {text: "Coordonnes", style: "title2"},
            {
                columns: [
                    {
                        style: "list",
                        width: '30%',
                        bold: true,
                        text: [
                            "Tlphone\n",
                            "Site web\n\n",
                            "Adresse"
                        ]

                    },
                    {
                        style: "list",
                        width: 'auto',
                        text: [
                            organisme.telephone + "\n",
                            organisme.web + "\n\n",
                            this.formatAddressToDisplay(organisme.adresse) + "\n"
                        ]
                    }
                ]
            }

        ];
        this.generatePdfAndOpen(title, "Fiche " + "Organisme", content);
    }

    generateMissionPdfAndOpen(mission: Mission) {
        let title = mission.numFacture + " - " + mission.nom;

        let collaborateursList: Array<any> = [];
        for (let lien of mission.liensCollaborateur) {
            collaborateursList.push({text: [{text: lien.role + " : ", bold: true}, lien.collaborateur.nom + "," + lien.collaborateur.prenom]});
        }

        let content;

        if (mission.isClosed) {
            content = [
                {text: title, style: "title1"},
                {
                    text: 'Rsum de la mission',
                    style: 'title2'
                },
                mission.synthese.resume,
                {
                    text: 'Besoins identifis',
                    style: 'title2'
                },
                mission.synthese.besoinsIdentifies,
                {
                    text: 'Objectifs / rsultats attendus',
                    style: 'title2'
                },
                mission.synthese.objectifs,
                {
                    text: 'Enjeu socital',
                    style: 'title2'
                },
                mission.synthese.enjeu,
                {
                    text: 'Mthode / dmarche adopte',
                    style: 'title2'
                },
                mission.synthese.methode,
                {
                    text: 'Contraintes / leviers identifis',
                    style: 'title2'
                },
                mission.synthese.contraintes,
                {
                    text: 'Pistes de valorisation',
                    style: 'title2'
                },
                mission.synthese.pistesValorisation,
                {
                    text: 'Mise en relation',
                    style: 'title2'
                },
                mission.synthese.miseEnRelation,
                {
                    text: 'Evolution',
                    style: 'title2'
                },
                mission.synthese.evolution
            ];
            this.generatePdfAndOpen(title, "Analyse " + "Mission", content);
        } else {
            content = [
                {text: title, style: "title1"},
                {
                    columns: [
                        {
                            style: "list",
                            width: '30%',
                            bold: true,
                            text: [
                                "Dure\n",
                                "Contact\n",
                                "Client\n",
                                "Financeur\n",
                                "Commentaires"
                            ]

                        },
                        {
                            style: "list",
                            width: 'auto',
                            text: [
                                this.formatDateToDisplay(mission.dateDebut) + " - " + this.formatDateToDisplay(mission.dateFin) + " pour une dure de " + mission.nbJours + " jour(s)\n",
                                mission.contact.nom + " " + mission.contact.prenom + "\n",
                                mission.client.nom + "\n",
                                mission.financeur.nom + "\n",
                                mission.commentaire + "\n"
                            ]
                        }
                    ]
                },
                {text: "Catgories", style: "title2"},
                {
                    columns: [
                        {
                            style: "list",
                            width: '30%',
                            bold: true,
                            text: [
                                "Programme\n",
                                "CDT\n\n",
                                "Type\n",
                                "Format\n",
                                "Thmatique\n",
                                "Cible\n",
                                "Modalit d'obtention\n",
                                "Modalit d'IS"
                            ]

                        },
                        {
                            style: "list",
                            width: 'auto',
                            text: [
                                mission.programme + "\n",
                                this.formatBooleanToDisplay(mission.cdt) + "\n\n",
                                mission.missionType + "\n",
                                mission.format + "\n",
                                mission.thematique + "\n",
                                mission.cible + "\n",
                                mission.modaliteObtention + "\n",
                                mission.modaliteIS + "\n"
                            ]
                        }
                    ]
                },
                {text: "Collaborateurs", style: "title2"},
                {
                    ul: collaborateursList
                },


            ];
            this.generatePdfAndOpen(title, "Information " + "Mission", content);
        }

    }


    generateProspectionPdfAndOpen(prospection: Prospection) {
        let title = prospection.nom;

        let content = [
            {text: title, style: "title1"},
            {
                columns: [
                    {
                        style: "list",
                        width: '30%',
                        bold: true,
                        text: [
                            "Statut\n",
                            "Dure\n",
                            "Contact\n",
                            "Client\n",
                            "Commentaires"
                        ]

                    },
                    {
                        style: "list",
                        width: 'auto',
                        text: [
                            prospection.statut + "\n",
                            this.formatDateToDisplay(prospection.dateContact) + " - " + this.formatDateToDisplay(prospection.dateCloture) + "\n",
                            prospection.contact.nom + " " + prospection.contact.prenom + "\n",
                            prospection.client.nom + "\n",
                            prospection.commentaire + "\n"
                        ]
                    }
                ]
            },
            {text: "Catgories", style: "title2"},
            {
                columns: [
                    {
                        style: "list",
                        width: '30%',
                        bold: true,
                        text: [
                            "Modalit d'obtention\n",
                            "Cible\n",
                            "Thmatique\n",
                            "Modalit d'IS"
                        ]

                    },
                    {
                        style: "list",
                        width: 'auto',
                        text: [
                            prospection.modaliteObtention + "\n",
                            prospection.cible + "\n",
                            prospection.thematique + "\n",
                            prospection.modaliteIS + "\n"
                        ]
                    }
                ]
            },
            {text: "Chiffrage", style: "title2"},
            {
                columns: [
                    {
                        style: "list",
                        width: '30%',
                        bold: true,
                        text: [
                            "Nombre de jours\n",
                            "Montant"
                        ]

                    },
                    {
                        style: "list",
                        width: 'auto',
                        text: [
                            prospection.nbJours + "\n",
                            prospection.montantProposition + "\n"
                        ]
                    }
                ]
            }
        ];
        this.generatePdfAndOpen(title, "Fiche " + "Prospection", content);

    }

    generateNotePdfAndOpen(note: Note) {
        let title = note.noteType;
        let collaborateursList: Array<any> = [];

        for (let col of note.collaborateursConcernes) {
            collaborateursList.push({text: col.nom + "  " + col.prenom, bold: true});
        }

        let content = [
            {text: title, style: "title1"},
            {
                columns: [
                    {
                        style: "list",
                        width: '30%',
                        bold: true,
                        text: [
                            "Rdacteur\n",
                            "Date\n",
                            "Lieu\n",
                        ]

                    },
                    {
                        style: "list",
                        width: 'auto',
                        text: [
                            note.auteur.nom + ' ' + note.auteur.prenom + "\n",
                            this.formatDateToDisplay(note.date) + "\n",
                            (note.lieu) ? note.lieu + "\n" : "",
                            this.formatAddressToDisplay(note.adresse)
                        ]
                    }
                ]
            },
            {
                columns: [
                    {
                        style: "list",
                        width: '30%',
                        bold: true,
                        text: [
                            "Collaborateurs concerns"
                        ]

                    },
                    {
                        style: "list",
                        width: 'auto',
                        ul: collaborateursList
                    }
                ]
            },
            {
                text: 'Objet ayant conduit  la rdaction du rapport',
                style: 'title2'
            },
            note.objetDeclencheur || "-",
            {
                text: 'Fait observ OU objet de la discussion avec lentreprise',
                style: 'title2'
            },
            note.faitObserve || "-",
            {
                text: 'Commentaire analytique OU synthse des changes avec lentreprise',
                style: 'title2'
            },
            note.commentaire || "-",
            {
                text: 'Impact sur les activits en cours',
                style: 'title2'
            },
            note.impactActivite || "-",
            {
                text: 'Contact',
                style: 'title2'
            },
            note.contact && note.contact.nom + ' ' + note.contact.prenom || "-",
            {
                text: 'Organisme',
                style: 'title2'
            },
            note.organisme && note.organisme.nom || "-"

        ];
        this.generatePdfAndOpen(title, "Fiche " + "Note", content);

    }

    generatePdfAndOpen(title: string, headerTitle: string, content: Array<any>) {

        let data = JSON.stringify(content).replace(/(null|undefined)/gi, "-");
        content = JSON.parse(data);

        var docDefinition = {
            color: '#616161',
            pageMargins: [40, 80, 40, 60],
            info: {
                title: title,
                author: 'smartis'
            },
            header: {
                style: 'header',
                margin: 10,
                columns: [
                    {
                        image: 'data:image/jpeg;base64,',
                        width: 150
                    },
                    {
                        alignment: 'center',
                        text: headerTitle,
                        margin: [0, 10, 0, 0]
                    }
                ]
            },
            content: content,
            footer: {
                style: 'footer',
                columns: [
                    {text: ' Ellyx ' + new Date().getFullYear()},
                ]
            },

            styles: {
                header: {
                    fontSize: 28,
                    bold: true
                },
                title1: {
                    fontSize: 20,
                    bold: true,
                    color: '#ec7808',
                    margin: [0, 20, 0, 10]
                },
                title2: {
                    fontSize: 16,
                    bold: true,
                    color: '#309cba',
                    margin: [0, 15, 0, 10]
                },
                list: {
                    lineHeight: 1.2,
                    margin: [10, 5]
                },
                footer: {
                    fontSize: 10,
                    alignment: 'center'
                }
            }
        }
        pdfMake.createPdf(docDefinition).open();

    }



    formatDateToDisplay(date: string) {
        if (!date) {
            return "?";
        }
        return moment(date).format("DD-MM-YYYY");
    }
    formatBooleanToDisplay(value: boolean) {
        if (value) {
            return "oui"
        }
        return "non";
    }
    formatAddressToDisplay(adresse: Adresse) {
        let formattedAddress = "";

        if (adresse) {
            formattedAddress += adresse.residence ? adresse.residence + "\n" : "";
            formattedAddress += adresse.numero + " " + adresse.rue + "\n";
            formattedAddress += adresse.complement ? adresse.complement + "\n" : "";
            formattedAddress += adresse.boitePostale ? adresse.boitePostale + "\n" : "";
            formattedAddress += adresse.codePostal + " " + adresse.ville;
            formattedAddress += adresse.cedex ? adresse.cedex : "";
        }
        return formattedAddress;
    }
    
    formatNonEmptyText(value: string){
        if(!value){
            return "-";
        }
        return value;
    }

}



