import {Component, OnInit, ViewChild} from '@angular/core';
import {ISubscription} from "rxjs/Subscription";
import {Observable} from 'rxjs/Observable';
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Contact, LienOrganisme, DocumentSyncEvent} from '../../model';
import {sanitize} from '../../utils';
import {Router} from '@angular/router';
import {LiensOrganismePipe} from '../../pipes/liens-organisme.pipe';
import {LiensOrganismeVillePipe} from '../../pipes/liens-organisme-ville.pipe';
import {EventService} from '../../services/event-service/event.service';
import {AuthService} from '../../services/auth/auth.service';
import {exportRowsToCsv} from '../../utils';


@Component({
    selector: 'app-contacts',
    templateUrl: './contacts.component.html',
    styleUrls: ['./contacts.component.scss']
})
export class ContactsComponent implements OnInit {
    private documentSyncEmitter: ISubscription;
    rows: Array<Contact> = [];
    datas: Array<Contact> = [];
    isAdmin$: Observable<boolean>;
    loadingIndicator: boolean = true;
    activeFilter: boolean = true;
    texteFilter: string;
    selected = [];
    @ViewChild('table') table: any;



    columns: any[] = [
        {prop: 'nom', flexGrow: 1},
        {prop: 'prenom', name: 'Prnom', flexGrow: 1},
        {prop: 'liensOrganisme', name: 'Organismes', flexGrow: 1, pipe: new LiensOrganismePipe()},
        //        {prop: 'liensOrganisme', name: 'Ville', flexGrow: 1, pipe: new LiensOrganismeVillePipe()},
    ];

    constructor(private eventService: EventService, private pouchdbservice: PouchdbService, private authService: AuthService, private router: Router) {
        this.isAdmin$ = this.authService.isAdmin;
    }

    ngOnDestroy() {
        if (this.documentSyncEmitter) {
            this.documentSyncEmitter.unsubscribe();
        }
    }

    ngOnInit() {
        this.loadData();
        this.documentSyncEmitter = this.eventService.getDocumentSyncEmitter().subscribe((item: DocumentSyncEvent) => {
            if (item.type === "Contact" || item.type === EventService.ALL) {
                this.ngOnInit();
            }
        });
    }

    loadData() {
        this.pouchdbservice.getAllContacts().subscribe((data) => {
            this.datas = data;
            this.filter();
            setTimeout(() => {this.loadingIndicator = false;}, 1500);
        });
    }

    getRowClass(row: Contact) {
        return {
            'deletedItem': row.isDeleted
        };
    }


    updateFilter(event) {
        let val = event.target.value;
        if (!val) {
            this.rows = this.datas;
            return;
        };
        this.texteFilter = sanitize(val);
        this.filter();
    }

    checkInOrganisme(liensOrganisme: Array<LienOrganisme>, valueToCheck): boolean {
        if (liensOrganisme) {
            for (let lien of liensOrganisme) {
                if (lien.organisme && lien.organisme.adresse && (sanitize(lien.organisme.adresse.ville).indexOf(valueToCheck) !== -1 || sanitize(lien.organisme.adresse.codePostal).indexOf(valueToCheck) !== -1)) {
                    return true;
                }
            }
        }
        return false;
    }


    onSelect(event) {
        var selected: Array<Contact> = event.selected;
        if (selected.length > 0) {
            this.router.navigate(['/contacts/' + selected[0]._id]);
        }
    }

    filterOnActif(value: boolean) {
        this.activeFilter = value;
        this.filter();
    }

    filter() {
        let self = this;
        const temp = this.datas.filter(function (d) {
            return (!self.texteFilter || sanitize(d.nom).indexOf(self.texteFilter) !== -1
                || sanitize(d.prenom).indexOf(self.texteFilter) !== -1
                || self.checkInOrganisme(d.liensOrganisme, self.texteFilter)
                || (d.liensOrganisme && d.liensOrganisme.filter(lien => sanitize(lien.organisme.nom).indexOf(self.texteFilter) !== -1).length > 0))
                && (!self.activeFilter || !d.isDeleted);
        });
        this.rows = temp;
        // Whenever the filter changes, always go back to the first page
        this.table.offset = 0;
    }

    exportCsv() {
        var headerKeys = ["civilite", "nom", "prenom", "mail", "telephone", "commentaire", "dateRencontre"];
        exportRowsToCsv(headerKeys, this.rows, 'Export_contacts');
    }

}


