import {Component, OnInit, Input} from '@angular/core';
import {ImputationCube} from '../../../model';
import {nest, sum} from 'd3';

@Component({
  selector: 'app-chart-programme',
  templateUrl: './chart-programme.component.html',
  styleUrls: ['./chart-programme.component.scss']
})
export class ChartProgrammeComponent implements OnInit {

  _datas: Array<ImputationCube> = [];

    @Input() set datas(values: Array<ImputationCube>) {
        this._datas = values;
        this.refreshData();
    }

    dataFiltered: any;

    constructor() {}

    ngOnInit() {
        this.refreshData();
    }

    refreshData() {
        var self = this;
        var datasWithFilter = this._datas.filter(function (d: ImputationCube) {return d.activite==="Programme"});
        
        this.dataFiltered = nest()
            .key(function (d: ImputationCube) {return d.label})
            .rollup(function (v) {return sum(v, function (d: ImputationCube) {return d.charge}) as any})
            .entries(datasWithFilter)
            .map(function (group) {
                return {
                    name: group.key,
                    value: group.value
                };
            });

    }

}



