import {Component, OnInit, OnDestroy, NgZone} from '@angular/core';
import {ISubscription} from "rxjs/Subscription";
import {Mission, LienMissionCollaborateur, LienEntreMission, Collaborateur, Contact, Organisme, Parametres, Prospection, DocumentSyncEvent, MissionAvancement} from '../../model';
import {FormGroup, FormBuilder, Validators, FormArray} from '@angular/forms';
import {ActivatedRoute} from "@angular/router";
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Router} from '@angular/router';
import {PdfGeneratorService} from '../../services/pdf-generator/pdf-generator.service';
import * as alertify from 'alertifyjs/build/alertify.js';
import {merge} from 'micro-dash';
import {environment} from '../../../environments/environment';
import {EventService} from '../../services/event-service/event.service';
import {AuthService} from './../../services/auth/auth.service';
import {Constant} from './../../constants';
import {ContactEditComponent} from '../../contact/edit/contact-edit.component';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';

@Component({
    selector: 'app-mission',
    templateUrl: './mission.component.html',
    styleUrls: ['./mission.component.scss']
})
export class MissionComponent implements OnInit, OnDestroy {
    dropbox_url = environment.dropbox_url;
    form: FormGroup;
    formSynthese: FormGroup;
    formSubmitAttempt: boolean = false;
    errorMessage: string;
    editForm = false;
    createForm = false;
    editSyntheseForm = false;
    mission: Mission = new Mission();
    collaborateurs: Array<Collaborateur> = [];
    contacts: Array<Contact> = [];
    organismes: Array<Organisme> = [];
    parametres: Parametres = new Parametres();
    missions: Array<Mission> = [];
    liensCollaborateur: FormArray;
    liensMission: FormArray;
    prospection: Prospection;
    rows: Array<MissionImputation> = [];
    chargeTotal: number = 0;
    currentCollaborateur: Collaborateur;
    isAdmin: boolean = false;
    isCP: boolean = false;
    documentSyncEmitter: ISubscription;



    constructor(private zone: NgZone, private eventService: EventService, private fb: FormBuilder, private route: ActivatedRoute,
        private pouchdbservice: PouchdbService, private router: Router, private pdfGeneratorService: PdfGeneratorService, private authService: AuthService,
        private modalService: NgbModal) {
        this.authService.isAdmin.subscribe((admin) => {
            this.isAdmin = admin;
        });
        this.authService.collaborateurObs.subscribe((collabo) => {
            this.currentCollaborateur = collabo;
        });

        this.form = this.fb.group({
            nom: ['', Validators.required],
            //            numFacture: [''],
            contact: this.fb.group({
                _id: ['', Validators.required]
            }),
            client: this.fb.group({
                _id: ['']
            }),
            financeur: this.fb.group({
                _id: ['']
            }),
            cdt: [''],
            programme: [''],
            dateDebut: ['', Validators.required],
            dateFin: ['', Validators.required],
            nbJours: [''],//{value: '', disabled: true}
            missionType: [''],
            format: [''],
            modaliteObtention: [''],
            montantProposition: [''],
            cible: [''],
            thematique: [''],
            modaliteIS: [''],
            commentaire: [''],
            restrictionAcces: [''],
            avancement: ['', Validators.required],
            //            dropboxRepertoire: [''],
            liensCollaborateur: this.fb.array([]),
            liensMission: this.fb.array([])

        });
        this.liensCollaborateur = this.form.get('liensCollaborateur') as FormArray;
        this.liensMission = this.form.get('liensMission') as FormArray;

        //        this.form.get('dateDebut').valueChanges.subscribe(data => {
        //            this.calculateNbJours();
        //        });
        //        this.form.get('dateFin').valueChanges.subscribe(data => {
        //            this.calculateNbJours();
        //        });

        this.formSynthese = this.fb.group({
            resume: ['', Validators.required],
            besoinsIdentifies: ['', Validators.required],
            objectifs: ['', Validators.required],
            enjeu: ['', Validators.required],
            methode: ['', Validators.required],
            contraintes: ['', Validators.required],
            pistesValorisation: ['', Validators.required],
            miseEnRelation: ['', Validators.required],
            evolution: ['', Validators.required],
        })
    }

    ngOnDestroy() {
        if (this.documentSyncEmitter) {
            this.documentSyncEmitter.unsubscribe();
        }
    }


    ngOnInit() {
        this.route.params.subscribe(
            params => {
                var id = params['id'];
                if (id == '0') {
                    this.form.reset();
                    this.formSynthese.reset();
                    this.editForm = false;
                    this.createForm = true;
                    this.isCP = true;
                    this.mission.addCP(this.currentCollaborateur);
                    this.form.patchValue(this.mission);
                } else {
                    this.loadMission(id);
                    this.documentSyncEmitter = this.eventService.getDocumentSyncEmitter().subscribe((item: DocumentSyncEvent) => {
                        if (item.type === "Mission" && item.ids.indexOf(id) != -1) {
                            if (this.editForm) {
                                alertify.warning("La mission a t modifie par un autre utilisateur, si vous enregistrez vos modifications, vous craserez les siennes")
                            }
                            this.loadMission(this.mission._id);
                        }
                    });
                    this.loadImputation(id);
                }
            });

        this.pouchdbservice.getCollaborateurs().subscribe(
            data => {
                this.collaborateurs = data;
            },
            err => {
                console.log(err)
            }
        );
        this.pouchdbservice.getOrganismes().subscribe(
            data => {
                this.organismes = data.sort((x, y) => x.nom.localeCompare(y.nom));
            },
            err => {
                console.log(err)
            }
        );
        this.pouchdbservice.getContacts().subscribe(
            data => {
                this.contacts = data.sort((x, y) => x.nom.localeCompare(y.nom));
            },
            err => {
                console.log(err)
            }
        );
        this.pouchdbservice.getMissions().subscribe(
            data => {
                this.missions = data.sort((x, y) => x.nom.localeCompare(y.nom));
            },
            err => {
                console.log(err)
            }
        );
        this.pouchdbservice.getParametres().subscribe(
            data => {
                this.parametres = data;
            },
            err => {
                console.log(err)
            }
        );
    }

    loadMission(id: string) {
        this.pouchdbservice.get(id).subscribe(
            data => {
                this.zone.run(() => {
                    this.mission = data;
                    if (!hasMissionAccessRight(this.mission, this.isAdmin, this.currentCollaborateur)) {
                        this.router.navigate(['/missions/']);
                    }
                    this.isCP = this.checkIsCP(this.mission, this.currentCollaborateur);
                });
                if (this.mission.prospectionId) {
                    this.pouchdbservice.get(this.mission.prospectionId).subscribe(
                        prosp => {
                            this.prospection = prosp;
                        }
                    );
                }
            },
            err => {
                console.log(err)
            }
        );
    }

    toggleEdit() {
        this.editForm = !this.editForm;
        if (this.editForm) {
            this.form.reset();
            this.formSynthese.reset();
            if (this.mission.isClosed) {
                this.formSynthese.patchValue(this.mission.synthese);
            } else {
                this.liensCollaborateur.controls = [];
                if (this.mission.liensCollaborateur) {
                    for (let lien of this.mission.liensCollaborateur) {
                        this.liensCollaborateur.push(this.createLienCollaborateur(lien));
                    }
                }
                this.liensMission.controls = [];
                if (this.mission.liensMission) {
                    for (let lien of this.mission.liensMission) {
                        this.liensMission.push(this.createLienMission(lien));
                    }
                }
                this.form.patchValue(this.mission);
            }
            // check deleted contact/organisme/collaborateur
            if (this.mission.liensCollaborateur) {
                this.mission.liensCollaborateur.forEach(lien => {
                    if (!this.collaborateurs.find(item => item._id === lien.collaborateur._id)) {
                        lien.collaborateur.isDeleted = true;
                        this.collaborateurs.push(lien.collaborateur);
                    }
                });
            }
            if (this.mission.contact && !this.contacts.find(item => item._id === this.mission.contact._id)) {
                this.mission.contact.isDeleted = true;
                this.contacts.push(this.mission.contact);
            }
            if (this.mission.client && !this.organismes.find(item => item._id === this.mission.client._id)) {
                this.mission.client.isDeleted = true;
                this.organismes.push(this.mission.client);
            }
            if (this.mission.financeur && !this.organismes.find(item => item._id === this.mission.financeur._id)) {
                this.mission.financeur.isDeleted = true;
                this.organismes.push(this.mission.financeur);
            }
        } else {
            this.editSyntheseForm = false;
            this.formSubmitAttempt = false;
        }
    }

    isFieldInvalid(field: string) {
        return (
            this.form.get(field).invalid &&
            (this.form.get(field).touched || (this.form.get(field).untouched && this.formSubmitAttempt))
        );
    }

    isSyntheseFieldInvalid(field: string) {
        return (
            this.formSynthese.get(field).invalid &&
            (this.formSynthese.get(field).touched || (this.formSynthese.get(field).untouched && this.formSubmitAttempt))
        );
    }

    isFieldInvalidInLienCollaborateur(index: number, field: string) {
        let control = this.liensCollaborateur.controls[index].get(field);
        return control.invalid && (control.touched || (control.untouched && this.formSubmitAttempt));
    }
    isFieldInvalidInLienMission(index: number, field: string) {
        let control = this.liensMission.controls[index].get(field);
        return control.invalid && (control.touched || (control.untouched && this.formSubmitAttempt));
    }

    closeMission() {
        let self = this;
        alertify.confirm('Afin de cloturer cette mission, vous devez remplir la fiche de synthse', function () {
            self.editForm = true;
            self.editSyntheseForm = true;
        });
    }
    changeAvancementMission() {
        alertify.warning("Attention le changement de l'tat d'avancement est dfinitif une fois la mission sauvegarde");
    }

    onMissionSubmit() {
        this.errorMessage = "";
        if (this.form.valid) {
            // check if CP is set
            let cpFound = false;
            if (this.form.getRawValue()['liensCollaborateur']) {
                this.form.getRawValue()['liensCollaborateur'].forEach((lien: LienMissionCollaborateur) => {
                    if (lien.role == Constant.MISSION_ROLE_CP) {
                        cpFound = true;
                    }
                });
            }
            if (!cpFound) {
                this.errorMessage = "Vous devez renseigner le chef de mission";
                return;
            }
            let currentAvancement = this.mission.avancement;
            merge(this.mission, this.form.getRawValue());
            if (this.mission.contact && this.mission.contact._id) {
                let contact: Contact = this.contacts.find(org => org._id === this.mission.contact._id);
                this.mission.contact.nom = contact.nom;
                this.mission.contact.prenom = contact.prenom;
            }
            if (this.mission.client && this.mission.client._id) {
                this.mission.client.nom = this.organismes.find(org => org._id === this.mission.client._id).nom;
            }
            if (this.mission.financeur && this.mission.financeur._id) {
                this.mission.financeur.nom = this.organismes.find(org => org._id === this.mission.financeur._id).nom;
            }
            if (this.mission.liensCollaborateur) {
                for (let lien of this.mission.liensCollaborateur) {
                    if (lien.collaborateur && lien.collaborateur._id) {
                        let collaborateur: Collaborateur = this.collaborateurs.find(org => org._id === lien.collaborateur._id);
                        if (collaborateur) {
                            lien.collaborateur.nom = collaborateur.nom;
                            lien.collaborateur.prenom = collaborateur.prenom;
                        }
                    }
                }
            }
            if (this.mission.liensMission) {
                for (let lien of this.mission.liensMission) {
                    if (lien.mission && lien.mission._id) {
                        lien.mission.nom = this.missions.find(org => org._id === lien.mission._id).nom;
                    }
                }
            }
            if (this.mission.restrictionAcces) {
                let restrictionAcces: Array<Collaborateur> = this.form.get('restrictionAcces').value;
                this.mission.restrictionAcces = [];
                for (let coll of restrictionAcces) {
                    let newColl = new Collaborateur();
                    newColl._id = coll._id;
                    newColl.nom = coll.nom;
                    newColl.prenom = coll.prenom;
                    this.mission.restrictionAcces.push(newColl);
                }
            }
            // historique avancement mission
            if (!this.mission._id || !this.mission.historiqueAvancement) {
                this.mission.historiqueAvancement = new Array<MissionAvancement>();
                let avancement = new MissionAvancement();
                this.mission.historiqueAvancement.push(avancement);
            } else if (currentAvancement !== this.mission.avancement) {
                this.mission.historiqueAvancement[this.mission.historiqueAvancement.length - 1].dateFin = new Date();
                let avancement = new MissionAvancement(this.mission.avancement);
                this.mission.historiqueAvancement.push(avancement);
            }
            this.saveMission(this.mission)

        } else {
            this.errorMessage = "Tous les champs obligatoires ne sont pas renseigns";
        }
        this.formSubmitAttempt = true;
    }

    onSyntheseSubmit() {
        if (this.formSynthese.valid) {
            if (!this.mission.dateCloture) {
                this.mission.dateCloture = new Date();
            }
            this.mission.isClosed = true;
            this.mission.synthese = this.formSynthese.value;
            if (this.mission.avancement != 100) {
                this.mission.avancement = 100;
                this.mission.historiqueAvancement[this.mission.historiqueAvancement.length - 1].dateFin = new Date();
                let avancement = new MissionAvancement(this.mission.avancement);
                this.mission.historiqueAvancement.push(avancement);
            }
            this.saveMission(this.mission)
        }
        this.formSubmitAttempt = true;
    }

    saveMission(mission: Mission) {
        this.pouchdbservice.save(mission).subscribe(
            response => {
                console.log(response);
                this.swithToShowMode(response.id)
            },
            err => {
                this.errorMessage = err;
            }
        );
    }


    swithToShowMode(id) {
        if (this.createForm) {
            this.createForm = false;
            this.editForm = false;
            this.router.navigate(['/missions/' + id]);
        } else {
            this.createForm = false;
            this.editForm = false;
            this.loadMission(id);
        };
    }

    createLienCollaborateur(lien: LienMissionCollaborateur): FormGroup {
        return this.fb.group({
            role: [lien.role, Validators.required],
            collaborateur: this.fb.group({
                _id: [lien.collaborateur._id, Validators.required]
            })
        });
    }

    addLienCollaborateur() {
        this.liensCollaborateur.push(this.createLienCollaborateur(new LienMissionCollaborateur()));
    }

    removeLienCollaborateur(index: number) {
        this.liensCollaborateur.removeAt(index);
    }

    createLienMission(lien: LienEntreMission): FormGroup {
        return this.fb.group({
            commentaire: [lien.commentaire, Validators.required],
            mission: this.fb.group({
                _id: [lien.mission._id, Validators.required]
            })
        });
    }

    addLienMission() {
        this.liensMission.push(this.createLienMission(new LienEntreMission()));
    }

    removeLienMission(index: number) {
        this.liensMission.removeAt(index);
    }

    //    calculateNbJours() {
    //        let dateDebut = this.form.get('dateDebut').value;
    //        let dateFin = this.form.get('dateFin').value;
    //        if (dateDebut && dateFin) {
    //            this.form.get('nbJours').setValue(Math.round((new Date(dateFin).getTime() - new Date(dateDebut).getTime()) / (1000 * 60 * 60 * 24)));
    //        }
    //    }

    compare(val1, val2) {
        if (!val1 || !val2) {
            return false;
        }
        return val1._id === val2._id;
    }

    openPdf() {
        this.pdfGeneratorService.generateMissionPdfAndOpen(this.mission);
    }


    loadImputation(missionId: string) {
        let self = this;
        this.pouchdbservice.getMissionImputations(missionId).subscribe(
            data => {
                this.chargeTotal = 0;
                let activites = data;
                let impCollab = new Map<string, MissionImputation>();
                activites.forEach(activite => {
                    if (!impCollab.has(activite.collaborateur._id)) {
                        let missionImputation = new MissionImputation();
                        missionImputation.collaborateur = activite.collaborateur.nom + " " + activite.collaborateur.prenom;
                        impCollab.set(activite.collaborateur._id, missionImputation);
                    }
                    let missionImputation = impCollab.get(activite.collaborateur._id);
                    activite.imputations.forEach(imputation => {
                        if (imputation.mission && imputation.mission._id === missionId) {
                            let total = 0;
                            imputation.charges.forEach(charge => total += charge.value);
                            self.chargeTotal += total;
                            missionImputation.charge += (total / 100);
                        }
                    });
                });
                this.chargeTotal = this.chargeTotal / 100;
                this.rows = Array.from(impCollab.values());
            },
            err => {
                alertify.warning("Les imputations ne peuvent tre affiches tant que vous n'tes pas synchronis");
            }
        );

    }

    checkIsCP(mission: Mission, currentCollaborateur: Collaborateur): boolean {
        if (mission.liensCollaborateur && mission.liensCollaborateur.length > 0) {
            let hasRight = false;
            mission.liensCollaborateur.forEach(lien => {
                if (currentCollaborateur && lien.collaborateur._id === currentCollaborateur._id) {
                    hasRight = true;
                    return;
                }
            });
            return hasRight;
        }
        return false;
    }

    addContact(event: Event) {
        event.preventDefault();
        const modalRef = this.modalService.open(ContactEditComponent, {size: 'lg'});
        modalRef.componentInstance.isModal = true;
        modalRef.result.then((_id) => {
            this.pouchdbservice.get(_id).subscribe(
                data => {
                    this.contacts.push(data);
                    this.contacts.sort((x, y) => x.nom.localeCompare(y.nom));
                    this.form.patchValue({contact: {_id: _id}});
                },
                err => {
                    console.log(err)
                }
            );
        }, (reason) => {
        });
    }
}


//class MissionSousImputation {
//    label: string;
//    charge: number;
//}

class MissionImputation {
    collaborateur: string;
    charge: number = 0;
    //    sousImputations: Array<MissionSousImputation> = new Array<MissionSousImputation>();
}

export function hasMissionAccessRight(mission: Mission, isAdmin: boolean, currentCollaborateur: Collaborateur): boolean {
    if (mission.restrictionAcces && mission.restrictionAcces.length > 0 && !isAdmin) {
        let hasRight = false;
        mission.restrictionAcces.forEach(collabo => {
            if (currentCollaborateur && collabo._id === currentCollaborateur._id) {
                hasRight = true;
                return;
            }
        });
        if (!hasRight) {
            alertify.error("Cette mission ncessite un droit d'accs");
            return false;
        }
    }
    return true;
}



