import {EventEmitter} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import PouchDB from 'pouchdb';
import PouchDBFind from 'pouchdb-find';
PouchDB.plugin(PouchDBFind);
import PouchDBAuthentication from 'pouchdb-authentication';
PouchDB.plugin(PouchDBAuthentication);
//PouchDB.plugin(PouchUpsert)

import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {EventService} from '../event-service/event.service';

export class PouchDbAdapter {

    dispatcher: EventEmitter<any> = new EventEmitter();


    private _pouchDB: PouchDB.Database;
    private _remotePouchDB: PouchDB.Database;
    private _db: PouchDB.Database;
    private _remoteCouchDBAddress: string;
    private _pouchDbName: string;

    // rxjs behaviour subjects to expose stats flags
    syncStatus = new BehaviorSubject<boolean>(false);
    couchDbUp = new BehaviorSubject<boolean>(true);

    constructor(private eventService: EventService, remoteCouchDBAddress: string, login: string, password: string) {
        this._remoteCouchDBAddress = remoteCouchDBAddress;
        // string function to extract the database name from the URL
        this._pouchDbName = remoteCouchDBAddress.substr(8, remoteCouchDBAddress.lastIndexOf('-'));

        //        this.checkRemoteDB();


        // new PouchDB(this._pouchDbName).destroy().then(function () {
        //     // database destroyed
        //     console.log('Local DB is Distroyed!!!!!!');

        // }).catch(function (err) {
        //     // error occurred
        // });
        // init local PouchDB
        this._pouchDB = new PouchDB(this._pouchDbName);

        this._remotePouchDB = new PouchDB(remoteCouchDBAddress, {skip_setup: true});


        let optionsSync = {
            live: true,
            retry: true,
            continuous: true,
            ajax: {
                withCredentials: true
            }
        };

        // do one way, one-off sync from the server until completion
        this._db = this._pouchDB;
        let self = this;
        this._remotePouchDB.logIn(login, password).then(function () {
            self._db = self._remotePouchDB;
            eventService.emitPouchDbEvent(true);
            self._pouchDB.replicate.from(self._remotePouchDB).on('complete', function (info) {
                self._db = self._pouchDB;
                console.log('complete');
                eventService.emitDocumentSyncEvent(EventService.ALL);
                self.syncStatus.next(true);
                self.couchDbUp.next(true);
                // then two-way, continuous, retriable sync
                let sync = self._pouchDB.sync(self._remotePouchDB, optionsSync)
                    .on('paused', info => {
                        console.log('paused' + info);
                        self.couchDbUp.next(true);
                    })
                    .on('paused', info => {
                        console.log('paused' + info);
                        self.checkRemoteDB();
                        self.syncStatus.next(true);
                    })
                    .on('change', info => {
                        console.log('change');
                        self.syncStatus.next(false);
                        if (info.change && info.change.docs) {
                            let types: Map<string, Array<string>> = new Map<string, Array<string>>();
                            let doc: any;
                            for (doc of info.change.docs) {
                                if (!types.has(doc.type)) {
                                    types.set(doc.type, [doc._id]);
                                } else {
                                    types.get(doc.type).push(doc._id)
                                }
                            }
                            types.forEach((ids: Array<string>, key: string) => {
                                eventService.emitDocumentSyncEvent(key, ids);
                            });

                        }
                    })
                    .on('denied', err => {
                        // a document failed to replicate (e.g. due to permissions)
                        console.log('Document failed to replicate (e.g. due to permissions)');
                        console.log(err);
                        if (err['status'] === 401) {
                            sync.cancel();
                            eventService.emitPouchDbEvent(false, "Identifiant ou mot de passe incorrect");
                            self.clear()
                        }
                    })
                    .on('error', err => {
                        console.log('PouchDB Error NFCs', err);
                        self.checkRemoteDB();
                        self.syncStatus.next(false);
                        if (err['status'] === 401) {
                            sync.cancel();
                            eventService.emitPouchDbEvent(false, "Identifiant ou mot de passe incorrect");
                            self.clear()
                        }
                    })
            }).on('error', function (info) {
                console.log('error :' + info);
                self.syncStatus.next(false);
                if (info['status'] === 401) {
                    eventService.emitPouchDbEvent(false, "Identifiant ou mot de passe incorrect");
                    self.clear()
                }
            });
        }).catch(function (error) {
            console.log(error);
            if (error['status'] === 401) {
                eventService.emitPouchDbEvent(false, "Identifiant ou mot de passe incorrect");
                self.clear()
            }
        });

    }

    clear() {
        this._remotePouchDB.close();
        this._pouchDB.destroy();
        this._pouchDB = null;
        this._remotePouchDB = null;
    }

    getDocsOfType(docType: string): Observable<any> {
        return new Observable(observer => {
            if (!this._pouchDB) {
                observer.error("No database connected");
                return;
            }
            this._pouchDB.find({
                selector: {type: docType}
                //                sort: ['name'],
                //                use_index: ['typeDesigndoc', 'typeIndex']
            }).then(function (result) {
                observer.next(result.docs);
            }).catch(function (err) {
                console.log(err);
                observer.error(err);
            });
        });
    }

    getDocsFromSelector(selector: any): Observable<any> {
        return new Observable(observer => {
            if (!this._pouchDB) {
                observer.error("No database connected");
                return;
            }
            this._pouchDB.find(selector).then(function (result) {
                observer.next(result.docs);
            }).catch(function (err) {
                console.log(err);
                observer.error(err);
            });
        });
    }

    getDocsFromSelectorRemote(selector: any): Observable<any> {
        return new Observable(observer => {
            if (!this._db) {
                observer.error("No database connected");
                return;
            }
            this._db.find(selector).then(function (result) {
                observer.next(result.docs);
            }).catch(function (err) {
                console.log(err);
                observer.error(err);
            });
        });
    }

    getDocFromQuery(map: any, key: string): Observable<any> {
        return new Observable(observer => {
            if (!this._pouchDB) {
                observer.error("No database connected");
                return;
            }
            this._pouchDB.query(map, {key: key, include_docs: true}).then(function (result) {
                if (result.rows) {
                    observer.next(result.rows.map(item => item.doc));
                } else {
                    observer.next([]);
                }
            }).catch(function (err) {
                console.log(err);
                observer.error(err);
            });
        });
    }

    post(doc): Observable<any> {
        return Observable.from(this._pouchDB.post(doc));
    }

    put(doc): Observable<any> {
        return Observable.from(this._pouchDB.put(doc));
    }

    get(docId: string): Observable<any> {
        if (!this._pouchDB) {
            return new Observable(observer => {
                observer.error("No database");
            });
        }
        return Observable.from(this._pouchDB.get(docId));
    }

    delete(docId: string, rev: string): Observable<any> {
        return Observable.from(this._pouchDB.put({_id: docId, _rev: rev, _deleted: true}));
    }

    putAttachment(docId: string, rev: string, attachmentId: string, myBase64String: string, contentType: string): Observable<any> {
        return Observable.from(this._pouchDB.putAttachment(docId, attachmentId, rev, myBase64String, contentType));
    }

    getAttachment(docId: string, attachmentId: string): Observable<any> {
        return new Observable(observer => {
            this._pouchDB.getAttachment(docId, attachmentId)
                .then((blob => {
                    var url = URL.createObjectURL(blob);
                    console.log('Stored a png blob of size: ' + url);
                    observer.next(url);
                    //                    URL.revokeObjectURL(url);
                }))
                .catch((error) => {
                    console.log("Aucun attachment de type " + attachmentId + " trouv");
                    observer.error(error);
                });
        });
    }



    // function to call the below functions
    // then update the rxjs BehaviourSubjects with the 
    // results
    private checkRemoteDB(): void {
        this.checkCouchUp()
            .then((result) => {
                this.couchDbUp.next(result);
            });
    }




    // fairly self explanatory function to make a 
    // GET http request to the URL and return false
    // if an error status or a timeout occurs, true if 
    // successful.
    private checkCouchUp(): Promise<boolean> {
        return new Promise<boolean>((resolve, reject) => {
            let xhr = new XMLHttpRequest();
            xhr.open('GET', this._remoteCouchDBAddress.substring(0, this._remoteCouchDBAddress.lastIndexOf("/")), true);
            xhr.onload = () => {
                if (xhr.status >= 200 && xhr.status < 300) {
                    resolve(true);
                } else {
                    resolve(false);
                }
            };
            xhr.onerror = () => {
                resolve(false);
            };
            xhr.send();
        });
    }




}





