import {Component, OnInit, ViewChild} from '@angular/core';
import {ISubscription} from "rxjs/Subscription";
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Note, Parametres, DocumentSyncEvent} from '../../model';
import {sanitize} from '../../utils';
import {Router} from '@angular/router';
import {EventService} from '../../services/event-service/event.service';

@Component({
    selector: 'app-notes',
    templateUrl: './notes.component.html',
    styleUrls: ['./notes.component.scss']
})
export class NotesComponent implements OnInit {
    private documentSyncEmitter: ISubscription;
    rows: Array<Note> = [];
    datas: Array<Note> = [];
    loadingIndicator: boolean = true;
    selected = [];
    yearFilter = [];
    currentYearFilter = new Date().getFullYear();
    currentTextFilter: string = "";
    currentTypeFilter: string = "";
    parametres: Parametres = new Parametres();
    @ViewChild('table') table: any;
    expanded: any = {};

    constructor(private eventService: EventService, private pouchdbservice: PouchdbService, private router: Router) {
        for (let i = 2016; i <= this.currentYearFilter; i++) {
            this.yearFilter.push(i);
        }
    }
    
    ngOnDestroy() {
        if (this.documentSyncEmitter) {
            this.documentSyncEmitter.unsubscribe();
        }
    }

    ngOnInit() {
        this.loadData();
        this.documentSyncEmitter = this.eventService.getDocumentSyncEmitter().subscribe((item: DocumentSyncEvent) => {
            if (item.type === "Note" || item.type === EventService.ALL) {
                this.loadData();
            }
        });
    }
    
    loadData() {
        this.pouchdbservice.getNotes().subscribe((data) => {
            this.datas = data;
            this.filter();
            setTimeout(() => {this.loadingIndicator = false;}, 1500);
        });
        this.pouchdbservice.getParametres().subscribe(
            data => {
                this.parametres = data;
            }
        );
    }



    updateFilter(value) {
        this.currentTextFilter = sanitize(value);
        this.filter();
    }
    
    updateFilterType(value) {
        this.currentTypeFilter = value;
        this.filter();
    }

    updateFilterYear(year) {
        this.currentYearFilter = year;
        this.filter();
    }

    filter() {
        let self = this;
        var from = new Date(this.currentYearFilter, 0, 1).getTime(); 
        var to = new Date(this.currentYearFilter, 11, 31).getTime();
        let tempData = this.datas.filter(function (d) {
            return new Date(d.date).getTime() <= to && new Date(d.date).getTime()>=from;
        });

        if (this.currentTextFilter) {
            tempData = tempData.filter(function (d) {
                return sanitize(d.noteType).indexOf(self.currentTextFilter) !== -1
                    || (d.auteur.nom && sanitize(d.auteur.nom).indexOf(self.currentTextFilter) !== -1)
                    || (d.auteur.prenom && sanitize(d.auteur.prenom).indexOf(self.currentTextFilter) !== -1)
                    || (d.contact && d.contact.nom && sanitize(d.contact.nom).indexOf(self.currentTextFilter) !== -1)
                    || (d.contact && d.contact.prenom && sanitize(d.contact.prenom).indexOf(self.currentTextFilter) !== -1)
                    || (d.organisme && d.organisme.nom && sanitize(d.organisme.nom).indexOf(self.currentTextFilter) !== -1)
                    || (d.lieu && sanitize(d.lieu).indexOf(self.currentTextFilter) !== -1);
            });
        }
        if (this.currentTypeFilter) {
            tempData = tempData.filter(function (d) {
                return d.noteType == self.currentTypeFilter;
            });
        }
        this.rows = tempData;
        // Whenever the filter changes, always go back to the first page
        this.table.offset = 0;
    }



    onSelect(event) {
        var selected: Array<Note> = event.selected;
        if (selected.length > 0) {
            this.router.navigate(['/notes/' + selected[0]._id]);
        }
    }

    toggleExpandRow(event: Event, row) {
        event.stopPropagation();
        event.preventDefault();
        console.log('Toggled Expand Row!', row);
        this.table.rowDetail.toggleExpandRow(row);
        return false;
    }



}



