import {EventEmitter, Injectable} from '@angular/core';
import {DocumentSyncEvent, PouchDbEvent} from '../../model';

@Injectable()
export class EventService {

    private dispatcherDocumentSync: EventEmitter<any> = new EventEmitter();
    private dispatcherPouchDb: EventEmitter<any> = new EventEmitter();
    public static ALL:string = "ALL";

    constructor() {}

    emitDocumentSyncEvent(type: string, ids: Array<string> = null) {
        let message = new DocumentSyncEvent();
        message.type = type;
        message.ids = ids;
        this.dispatcherDocumentSync.emit(message);
    }
    
    emitPouchDbEvent(isConnected : boolean, message:string = null) {
        let event = new PouchDbEvent();
        event.connected = isConnected;
        event.message = message;
        this.dispatcherPouchDb.emit(event);
    }



    getDocumentSyncEmitter() {
        return this.dispatcherDocumentSync;
    }
    getPouchDbEmitter() {
        return this.dispatcherPouchDb;
    }

}



