import {Component, OnInit} from '@angular/core';
import {PouchdbService} from '../services/pouchdb-service/pouchdb.service';
import {Activite, Parametres, Collaborateur, Mission, Imputation, Item, ImputationLienEntite, Contact, Organisme} from '../model';
import {Observable} from 'rxjs/Observable';
import {ActivatedRoute} from "@angular/router";
import {AuthService} from '../services/auth/auth.service';
import {getActiviteDateOfWeek} from '../utils';
import * as moment from 'moment';
import * as alertify from 'alertifyjs/build/alertify.js';

@Component({
    selector: 'app-activite',
    templateUrl: './activite.component.html',
    styleUrls: ['./activite.component.scss']
})
export class ActiviteComponent implements OnInit {

    activite: Activite = new Activite();
    missions: Array<Mission> = new Array<Mission>();
    contacts: Array<Contact> = new Array<Contact>();
    contactsFiltered: Array<Contact> = new Array<Contact>();
    organismes: Array<Organisme> = new Array<Organisme>();
    collaborateurs: Array<Collaborateur> = new Array<Collaborateur>();
    parametres: Parametres = new Parametres();
    collaborateurConnecte: Collaborateur;
    displayCollaborateurId: string;
    displayCollaborateur: Collaborateur;
    errorMessage: string;
    dateToDisplay = new Array();
    currentDate = moment();
    currentYear: number;
    currentWeek: number;
    currentStartDateOfWeek: string;
    currentEndDateOfWeek: string;
    numberOfWeek: number = moment().isoWeeksInYear();
    actualYear: number = moment().year();
    newActivite: string = "";
    newMissionId: string = "";
    newContactId: string = "";
    newOrganismeId: string = "";
    newCdt: string = "";
    newRecherche: string = "";
    newProgramme: string;
    fakeWeeksArray = new Array(53);
    isAdmin$: Observable<boolean>;
    collapse: string;
    editMode = false;

    constructor(private pouchdbservice: PouchdbService, private authService: AuthService, private route: ActivatedRoute) {
        this.isAdmin$ = this.authService.isAdmin;
    }

    ngOnInit() {
        this.route.params.subscribe(
            params => {
                var date = params['date'];
                if (date) {
                    this.currentDate = moment(date);
                }
                this.authService.collaborateurObs.subscribe(collaborateur => {
                    if (collaborateur) {
                        this.collaborateurConnecte = collaborateur;
                        this.displayCollaborateurId = collaborateur._id;
                        this.displayCollaborateur = collaborateur;
                        this.loadData();
                        this.renderTimesheet(this.currentDate);
                    }
                });
            }
        );
        this.authService.isAdmin.subscribe(isAdmin => {
            if (isAdmin) {
                this.pouchdbservice.getCollaborateurs().subscribe(
                    data => {
                        this.collaborateurs = data;
                        this.pouchdbservice.getActiviteForSemaine(moment().year(), moment().isoWeeks()).subscribe(
                            datas => {
                                for (let data of datas) {
                                    let col = this.collaborateurs.find(item => item._id === data.collaborateur._id);
                                    if (col) {
                                        col['isComplete'] = data.isComplete;
                                    }
                                }
                            },
                            err => {
                                this.errorMessage = err;
                            }
                        );
                    },
                    err => {
                        this.errorMessage = err;
                    }
                );

            }
        });
        this.pouchdbservice.getParametres().subscribe(
            data => {
                this.parametres = data;
                this.pouchdbservice.getMissions().subscribe(
                    data => {
                        this.missions = data.sort((a, b) => {
                            return a.numFacture ? -(a.numFacture + "").localeCompare(b.numFacture) : 1
                        });
                    },
                    err => {
                        this.errorMessage = err;
                    }
                );
                this.pouchdbservice.getContacts().subscribe(
                    data => {
                        this.contacts = data.sort((a, b) => {
                            return a.nom ? a.nom.localeCompare(b.nom) : 1
                        });
                        this.contactsFiltered = this.contacts;
                    },
                    err => {
                        this.errorMessage = err;
                    }
                );
                this.pouchdbservice.getOrganismes().subscribe(
                    data => {
                        this.organismes = data.sort((a, b) => {
                            return a.nom ? a.nom.localeCompare(b.nom) : 1
                        });
                    },
                    err => {
                        this.errorMessage = err;
                    }
                );
            },
            err => {
                this.errorMessage = err;
            }
        );

    }

    loadData() {
        this.editMode = false;
        if (this.displayCollaborateurId) {
            this.pouchdbservice.getActiviteForCollaborateurAndSemaine(this.displayCollaborateurId, this.currentDate.year(), this.currentDate.isoWeeks()).subscribe(
                data => {
                    if (!data || data.length == 0) {
                        // if no data copy previous week to the current without charges
                        let previousweek = moment(this.currentDate).add(-1, 'weeks');
                        this.pouchdbservice.getActiviteForCollaborateurAndSemaine(this.displayCollaborateurId, previousweek.year(), previousweek.isoWeeks()).subscribe(
                            data => {

                                let activite = new Activite();
                                activite.semaine = this.currentDate.isoWeeks();
                                activite.annee = this.currentDate.year();
                                activite.date = getActiviteDateOfWeek(this.currentDate);
                                activite.collaborateur._id = this.displayCollaborateur._id;
                                activite.collaborateur.nom = this.displayCollaborateur.nom;
                                activite.collaborateur.prenom = this.displayCollaborateur.prenom;
                                if (data && data.length > 0) {
                                    let previsousActivite = data[0];
                                    for (let imp of previsousActivite.imputations) {
                                        imp.charges = [{value: 0}, {value: 0}, {value: 0}, {value: 0}, {value: 0}, {value: 0}, {value: 0}];
                                        for (let sousimp of imp.sousImputations) {
                                            sousimp.charges = [{value: 0}, {value: 0}, {value: 0}, {value: 0}, {value: 0}, {value: 0}, {value: 0}];
                                        }
                                        activite.imputations.push(imp);
                                    }
                                }
                                this.activite = activite;
                            });
                    } else {
                        this.activite = data[0];
                    }
                },
                err => {
                    this.errorMessage = err;
                }
            );
        }


    }



    addLine() {
        if (!this.newActivite) {
            return;
        }
        if (this.newActivite === "Mission") {
            if (!this.newMissionId) {
                alertify.warning('Vous devez slectionner une mission');
                return;
            }
            this.activite.imputations.push(this.createImputationMission(this.newMissionId));
        }
        else if (this.newActivite === "Recherche") {
            if (!this.newRecherche) {
                alertify.warning('Vous devez slectionner une programme de recherche');
                return;
            }
            this.activite.imputations.push(this.createImputationRecherche(this.newRecherche));
        } else if (this.newActivite === "CDT") {
            if (!this.newContactId) {
                alertify.warning('Vous devez slectionner un contact');
                return;
            }
            if (!this.newCdt) {
                alertify.warning('Vous devez slectionner une catgorie de cdt');
                return;
            }
            this.activite.imputations.push(this.createImputationCDT(this.newCdt, this.newOrganismeId, this.newContactId));
        } else if (this.newActivite === "Programme") {
            if (!this.newProgramme) {
                alertify.warning('Vous devez slectionner un programme');
                return;
            }
            this.activite.imputations.push(this.createImputationProgramme(this.newProgramme));
        } else {
            let imp: Imputation = new Imputation();
            imp.activite = this.newActivite;
            this.activite.imputations.push(imp);
        }
        this.newMissionId = "";
        this.newRecherche = "";
        this.newActivite = "";
        this.newCdt = "";
        this.newContactId = "";
        this.newOrganismeId = "";

    }

    createImputationProgramme(programme: string): Imputation {
        let imp: Imputation = new Imputation();
        imp.activite = "Programme";
        imp.label = programme;
        imp.categorieNom = programme;
        return imp;
    }
    createImputationCDT(cdtNum: string, organismeId: string = null, contactId: string = null): Imputation {
        let imp: Imputation = new Imputation();
        imp.activite = "CDT";
        let cdt = this.parametres.cdt.find(pgm => pgm.sousItems.some(item => item.numero === cdtNum));
        let sousItem = cdt.sousItems.find(item => item.numero === cdtNum);
        imp.categorieId = cdt.numero;
        imp.categorieNom = cdt.nom;
        imp.label = "CDT " + sousItem.numero;
        imp.sousCategorieNom = sousItem.nom;
        imp.sousCategorieId = sousItem.numero;
        if (organismeId) {
            let organisme = this.organismes.find(item => item._id === organismeId);
            imp.organisme = new ImputationLienEntite(organisme._id, "Organisme", organisme.nom, organisme.nom);
        }
        if (contactId) {
            let contact = this.contacts.find(item => item._id === contactId);
            imp.contact = new ImputationLienEntite(contact._id, "Organisme", contact.nom + " " + contact.prenom, contact.nom);
        }
        return imp;
    }
    createImputationRecherche(rechercheNum: string): Imputation {
        let imp: Imputation = new Imputation();
        imp.activite = "Recherche";
        let workpackage = this.parametres.workpackages.find(pgm => pgm.sousItems.some(item => item.numero === rechercheNum));
        let sousItem = workpackage.sousItems.find(item => item.numero === rechercheNum);
        imp.categorieId = workpackage.numero;
        imp.categorieNom = workpackage.nom;
        imp.label = "WP " + sousItem.numero;
        imp.sousCategorieNom = sousItem.nom;
        imp.sousCategorieId = sousItem.numero;
        return imp;
    }
    createImputationMission(missionId: string): Imputation {
        let imp: Imputation = new Imputation();
        imp.activite = "Mission";
        let mission = this.missions.find(item => item._id === missionId);
        imp.mission = new ImputationLienEntite(missionId, "Mission", mission.nom, mission.numFacture);
        if (mission.contact) {
            imp.contact = new ImputationLienEntite(mission.contact._id, "Contact", mission.contact.nom + " " + mission.contact.prenom, mission.contact.nom);
        }
        if (mission.client) {
            imp.organisme = new ImputationLienEntite(mission.client._id, "Organisme", mission.client.nom, mission.client.nom);
        }
        imp.label = mission.numFacture;
        return imp;
    }



    addRechercheToImputation(imputation: Imputation, rechercheNum: string) {
        if (rechercheNum) {
            imputation.sousImputations.push(this.createImputationRecherche(rechercheNum));
            this.sortSousImputation(imputation.sousImputations);
        }
    }
    addCDTToImputation(imputation: Imputation, cdtNum: string) {
        if (cdtNum) {
            imputation.sousImputations.push(this.createImputationCDT(cdtNum));
            this.sortSousImputation(imputation.sousImputations);
        }
    }
    addProgrammeToImputation(imputation: Imputation, rechercheNum: string) {
        if (rechercheNum) {
            imputation.sousImputations.push(this.createImputationProgramme(rechercheNum));
            this.sortSousImputation(imputation.sousImputations);
        }
    }
    sortSousImputation(sousImputations: Array<Imputation>) {
        sousImputations = sousImputations.sort((a, b) => a.activite.localeCompare(b.activite));

    }
    removeSousImputation(imputation: Imputation, sousImputation: Imputation) {
        imputation.sousImputations = imputation.sousImputations.filter(obj => obj !== sousImputation);
        if (!this.editMode) {
            this.save();
        }
    }
    removeImputation(imputation: Imputation) {
        this.activite.imputations = this.activite.imputations.filter(obj => obj !== imputation);
        if (!this.editMode) {
            this.save();
        }
    }
    editLigne(imputation: Imputation, edit: boolean) {
        if (!edit) {
            if(!this.save()){
                return;
            };
        }
        this.editMode = edit;
    }

    changeCollaborateur() {
        this.displayCollaborateur = this.collaborateurs.find(item => item._id === this.displayCollaborateurId);
        this.loadData();
    }

    changeWeek() {
        this.currentDate.isoWeeks(this.currentWeek);
        this.renderTimesheet(this.currentDate);
        this.loadData();
    }

    changeYear() {
        this.currentDate.year(this.currentYear);
        this.renderTimesheet(this.currentDate);
        this.loadData();
    }

    previous() {
        let newDate = moment(this.currentDate).add(-1, 'weeks');
        if (newDate.year() < this.actualYear - 1) {
            return;
        }
        this.currentDate = newDate;
        this.renderTimesheet(this.currentDate);
        this.loadData();
    }

    next() {
        let newDate = moment(this.currentDate).add(1, 'weeks');
        if (newDate.year() > this.actualYear) {
            return;
        }
        this.currentDate = newDate;
        this.loadData();
        this.renderTimesheet(this.currentDate);
    }

    save():boolean {
        this.errorMessage = "";
        if (this.activite.imputations.length > 0) {
            // check CDT description
            if(this.editMode){
                for (let imp of this.activite.imputations) {
                    if (imp.activite == 'CDT' && !imp.description) {
                        this.errorMessage = "La description est obligatoire pour le CDT";
                        return false;
                    }
                    if (imp.activite == 'Mission') {
                        for (let sousImp of imp.sousImputations) {
                            if (sousImp.activite == 'CDT' && !sousImp.description) {
                                this.errorMessage = "La description est obligatoire pour le CDT";
                                return false;
                            }
                        }
                    }
                }
            }
            this.activite.isComplete = this.isComplete();
            this.pouchdbservice.save(this.activite).subscribe(
                result => {
                    this.activite._id = result.id;
                    this.activite._rev = result.rev;
                }
            );
        }
        this.collapse = "";
        return true;
    }

    renderTimesheet(date) {
        let weekDate = moment(date);
        weekDate = weekDate.startOf('isoWeek');
        this.currentYear = weekDate.year();
        this.currentWeek = weekDate.isoWeeks();
        this.numberOfWeek = weekDate.isoWeeksInYear();
        this.currentStartDateOfWeek = weekDate.format("L");
        this.dateToDisplay.length = 0;
        for (let i = 0; i < 7; i++) {
            this.dateToDisplay.push({numero: weekDate.date(), label: weekDate.format("dd")});
            weekDate.add(1, 'd');
        }
        this.currentEndDateOfWeek = weekDate.format("L");

    }

    getImputationsPourActivite(typeActivite: string): Array<Imputation> {
        return this.activite.imputations.filter(item => item.activite === typeActivite).sort((a, b) => a.label.localeCompare(b.label));
    }
    getImputationsAutre(): Array<Imputation> {
        return this.activite.imputations.filter(item => item.activite !== "Mission").sort((a, b) => a.activite.localeCompare(b.activite));
    }

    getRowspanActivite(typeActivite: string): Number {
        let rowspan = -1;
        for (let ligne of this.activite.imputations.filter(item => item.activite === typeActivite)) {
            rowspan += ligne.sousImputations.length + 2;
        }
        return rowspan;
    }



    getTotaux(): Array<number> {
        var result: Array<number> = [0, 0, 0, 0, 0, 0, 0];
        for (let ligne of this.activite.imputations) {
            for (let i = 0; i < result.length; i++) {
                result[i] += ligne.charges[i].value || 0;
            }
        }
        return result;
    }

    isComplete(): boolean {
        let result: number = 0;
        for (let ligne of this.activite.imputations) {
            for (let charge of ligne.charges) {
                result += charge.value || 0;
            }
        }
        return result >= 500;
    }

    changeCollapse(id: string) {
        this.collapse = this.collapse === id ? "" : id;
    }

    filterContacts() {
        if (!this.newOrganismeId) {
            this.contactsFiltered = this.contacts;
            return;
        }
        this.contactsFiltered = this.contacts.filter(item => {
            if (!item.liensOrganisme || !this.newOrganismeId) {
                return false;
            }
            let orga = item.liensOrganisme.find(lien => lien.organisme._id == this.newOrganismeId);
            return orga ? true : false;
        })
    }

    getPlaceholder(activite: string, sousCategorieId: string) {
        if (activite === 'CDT') {
            let cdt: Item[] = this.parametres.cdt.map(pgm => pgm.sousItems.find(item => item.numero === sousCategorieId)).filter(Boolean);
            if (cdt && cdt[0]) {
                return cdt[0].commentaire;
            }
        } else if (activite === 'Recherche') {
            let recherche: Item[] = this.parametres.workpackages.map(pgm => pgm.sousItems.find(item => item.numero === sousCategorieId)).filter(Boolean);
            if (recherche && recherche[0]) {
                return recherche[0].commentaire;
            }
        }
        return "";
    }

}




