import {Component, OnInit, ViewChild} from '@angular/core';
import {ISubscription} from "rxjs/Subscription";
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Prospection, Parametres, DocumentSyncEvent} from '../../model';
import {sanitize} from '../../utils';
import {Router} from '@angular/router';
import {EventService} from '../../services/event-service/event.service';
import {exportRowsToCsv} from '../../utils';

@Component({
    selector: 'app-prospections',
    templateUrl: './prospections.component.html',
    styleUrls: ['./prospections.component.scss']
})
export class ProspectionsComponent implements OnInit {
    private documentSyncEmitter: ISubscription;
    rows: Array<Prospection> = [];
    datas: Array<Prospection> = [];
    loadingIndicator: boolean = true;
    selected = [];
    yearFilter = [];
    currentYearFilter = new Date().getFullYear();
    currentTextFilter: string = "";
    currentStatutFilter: string = "";
    parametres: Parametres = new Parametres();
    @ViewChild('table') table: any;
    expanded: any = {};



    constructor(private eventService: EventService, private pouchdbservice: PouchdbService, private router: Router) {
        for (let i = 2016; i <= this.currentYearFilter; i++) {
            this.yearFilter.push(i);
        }
    }
    
    ngOnDestroy() {
        if (this.documentSyncEmitter) {
            this.documentSyncEmitter.unsubscribe();
        }
    }

    ngOnInit() {
        this.loadData();
        this.documentSyncEmitter = this.eventService.getDocumentSyncEmitter().subscribe((item: DocumentSyncEvent) => {
            if (item.type === "Prospection" || item.type === EventService.ALL) {
                this.loadData();
            }
        });
    }
    
    loadData() {
        this.pouchdbservice.getProspections().subscribe((data) => {
            this.datas = data;
            this.filter();
            setTimeout(() => {this.loadingIndicator = false;}, 1500);
        });
        this.pouchdbservice.getParametres().subscribe(
            data => {
                this.parametres = data;
            }
        );
    }



    updateFilter(value) {
        this.currentTextFilter = sanitize(value);
        this.filter();
    }
    
    updateFilterStatut(value) {
        this.currentStatutFilter = value;
        this.filter();
    }

    updateFilterYear(year) {
        this.currentYearFilter = year;
        this.filter();
    }

    filter() {
        let self = this;
        var from = new Date(this.currentYearFilter, 0, 1).getTime(); 
        var to = new Date(this.currentYearFilter, 11, 31).getTime();
        let tempData = this.datas.filter(function (d) {
            return new Date(d.dateContact).getTime() <= to && (!d.dateCloture || new Date(d.dateCloture).getTime()>=from);
        });

        if (this.currentTextFilter) {
            tempData = tempData.filter(function (d) {
                return sanitize(d.nom).indexOf(self.currentTextFilter) !== -1
                    || (d.contact && sanitize(d.contact.nom).indexOf(self.currentTextFilter) !== -1)
                    || (d.client && sanitize(d.client.nom).indexOf(self.currentTextFilter) !== -1);
            });
        }
        if (this.currentStatutFilter) {
            tempData = tempData.filter(function (d) {
                return d.statut == self.currentStatutFilter;
            });
        }
        this.rows = tempData;
        // Whenever the filter changes, always go back to the first page
        this.table.offset = 0;
    }



    onSelect(event) {
        var selected: Array<Prospection> = event.selected;
        if (selected.length > 0) {
            this.router.navigate(['/prospections/' + selected[0]._id]);
        }
    }
    
    exportCsv() {
        var headerKeys = ["nom","numProspection","contact.nom","client.nom","dateContact","dateCloture","modaliteObtention","cible","thematique",
        "modaliteIS","commentaire","montantProposition","nbJours","modaliteOffre","dropboxRepertoire","statut","dateChangementStatut"];
        exportRowsToCsv(headerKeys, this.rows, 'Export_prospection');
    }

    toggleExpandRow(event:Event, row) {
        event.stopPropagation();
        event.preventDefault();
        console.log('Toggled Expand Row!', row);
        this.table.rowDetail.toggleExpandRow(row);
        return false;
    }



}



