import {Component, OnInit} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {Contact} from '../../model';
import {ActivatedRoute} from "@angular/router";
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Router} from '@angular/router';
import {AuthService} from './../../services/auth/auth.service';
import {PdfGeneratorService} from '../../services/pdf-generator/pdf-generator.service';
import * as alertify from 'alertifyjs/build/alertify.js';

@Component({
    selector: 'app-contact',
    templateUrl: './contact.component.html',
    styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
    errorMessage: String;
    contact: Contact = new Contact();
    isAdmin$: Observable<boolean>;

    constructor(private route: ActivatedRoute, private pouchdbservice: PouchdbService,
        private router: Router, private pdfGeneratorService: PdfGeneratorService,
        private authService: AuthService) {
        this.isAdmin$ = this.authService.isAdmin;
    }

    ngOnInit() {
        this.route.params.subscribe(
            params => {
                var id = params['id'];
                if (id == '0') {
                    this.router.navigate(['/contacts/' + id + '/edit']);
                } else {
                    this.loadContact(id);
                }
            });

    }

    loadContact(id: string) {
        this.pouchdbservice.get(id).subscribe(
            data => {
                this.contact = data;
            },
            err => {
                this.errorMessage = err;
            }
        );
    }


    edit() {
        this.router.navigate(['/contacts/' + this.contact._id + '/edit']);
    }


    save() {
        this.pouchdbservice.save(this.contact).subscribe(
            response => {
                console.log(response);
            },
            err => {
                this.errorMessage = err;
            }
        );
    }

    undeleteContact() {
        let self = this;
        alertify.confirm('Etes-vous sr de vouloir ractiver ce contact', function () {
            self.contact.isDeleted = false;
            self.save();
        });
    }

    deleteContact() {
        let self = this;
        alertify.confirm('Etes-vous sr de vouloir supprimer ce contact', function () {
            self.contact.isDeleted = true;
            self.save();
        });
    }


    openPdf() {
        this.pdfGeneratorService.generateContactPdfAndOpen(this.contact);
    }

}



