import {Component, OnInit, ViewChild} from '@angular/core';
import {ISubscription} from "rxjs/Subscription";
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Mission, DocumentSyncEvent, Collaborateur} from '../../model';
import {sanitize} from '../../utils';
import {Router} from '@angular/router';
import {LiensOrganismePipe} from '../../pipes/liens-organisme.pipe';
import {EventService} from '../../services/event-service/event.service';
import {exportRowsToCsv, formatMomentInString, parseStringToDate} from '../../utils';
import {AuthService} from './../../services/auth/auth.service';
import * as alertify from 'alertifyjs/build/alertify.js';
import * as moment from 'moment';
import {hasMissionAccessRight} from '../edit/mission.component';

@Component({
    selector: 'app-missions',
    templateUrl: './missions.component.html',
    styleUrls: ['./missions.component.scss']
})
export class MissionsComponent implements OnInit {
    private documentSyncEmitter: ISubscription;
    rows: Array<Mission> = [];
    datas: Array<Mission> = [];
    loadingIndicator: boolean = true;
    selected = [];
    dateDebut: string = formatMomentInString(moment().startOf('year'));
    dateFin: string = formatMomentInString(moment());

    currentTextFilter: string = "";
    currentStatutFilter: string = "";
    currentCollaborateur: Collaborateur;
    isAdmin: boolean = false;
    @ViewChild('table') table: any;
    expanded: any = {};


    constructor(private eventService: EventService, private pouchdbservice: PouchdbService, private router: Router, private authService: AuthService) {
        this.authService.isAdmin.subscribe((admin) => {
            this.isAdmin = admin;
        });
        this.authService.collaborateurObs.subscribe((collabo) => {
            this.currentCollaborateur = collabo;
        });
    }

    ngOnDestroy() {
        if (this.documentSyncEmitter) {
            this.documentSyncEmitter.unsubscribe();
        }
    }

    ngOnInit() {
        this.loadData();
        this.documentSyncEmitter = this.eventService.getDocumentSyncEmitter().subscribe((item: DocumentSyncEvent) => {
            if (item.type === "Mission" || item.type === EventService.ALL) {
                this.loadData();
            }
        });
    }

    loadData() {
        this.pouchdbservice.getMissions().subscribe((data) => {
            this.datas = data;
            this.filter();
            setTimeout(() => {this.loadingIndicator = false;}, 1500);
        });
    }

    updateFilter(value) {
        this.currentTextFilter = sanitize(value);
        this.filter();
    }

    changeDate() {
        if (this.dateFin < this.dateDebut) {
            this.dateFin = formatMomentInString(moment(this.dateDebut).endOf('month'));
        }
        this.filter();
    }

    filter() {
        let self = this;
        var from: moment.Moment = moment(this.dateDebut);
        var to: moment.Moment = moment(this.dateFin);
        self = this;
        let tempData = this.datas.filter(function (d) {
            let dateFin = moment(d.dateFin);
            let dateDebut = moment(d.dateDebut);
            return from.isSameOrBefore(dateFin) && to.isSameOrAfter(dateDebut);
        });

        if (this.currentTextFilter) {
            tempData = tempData.filter(function (d) {
                return sanitize(d.nom).indexOf(self.currentTextFilter) !== -1
                    || (d.contact && sanitize(d.contact.nom).indexOf(self.currentTextFilter) !== -1)
                    || (d.client && sanitize(d.client.nom).indexOf(self.currentTextFilter) !== -1)
                    || (d.financeur && sanitize(d.financeur.nom).indexOf(self.currentTextFilter) !== -1);
            });
        }
        this.rows = tempData;
        // Whenever the filter changes, always go back to the first page
        this.table.offset = 0;
    }



    onSelect(event) {
        var selected: Array<Mission> = event.selected;
        if (selected.length > 0) {
            let selectedMission = selected[0];
            if (hasMissionAccessRight(selectedMission, this.isAdmin, this.currentCollaborateur)) {
                this.router.navigate(['/missions/' + selected[0]._id]);
                return true;
            }
            return false;

        }
    }

    exportCsv() {
        var headerKeys = ["nom", "numFacture", "contact.nom", "client.nom", "financeur.nom", "cdt", "programme", "dateDebut", "dateFin", "nbJours", "missionType", "format",
            "dateCloture", "isClosed", "modaliteObtention", "cible", "thematique", "modaliteIS", "commentaire", "dropboxRepertoire", "avancement",
            "synthese.resume", "synthese.besoinsIdentifies", "synthese.objectifs", "synthese.enjeu", "synthese.methode", "synthese.contraintes", "synthese.pistesValorisation", "synthese.miseEnRelation", "synthese.evolution"];
        exportRowsToCsv(headerKeys, this.rows, 'Export_mission');
    }

    toggleExpandRow(event: Event, row) {
        event.stopPropagation();
        event.preventDefault();
        console.log('Toggled Expand Row!', row);
        this.table.rowDetail.toggleExpandRow(row);
        return false;
    }


}







