import {Component, OnInit, ViewChild, ElementRef} from '@angular/core';
import {ISubscription} from "rxjs/Subscription";
import {PouchdbService} from '../services/pouchdb-service/pouchdb.service';
import {AuthService} from '../services/auth/auth.service';
import {Collaborateur, Action, ActionStatut, DocumentSyncEvent, Activite, Mission, Tache} from '../model';
import {EventService} from '../services/event-service/event.service';
import {getActiviteDateOfWeek} from '../utils';
import * as moment from 'moment';

@Component({
    selector: 'app-home',
    templateUrl: './home.component.html',
    styleUrls: [
        './home.component.scss',
        '../../assets/fonts/icofont/css/icofont.scss'
    ]
})
export class HomeComponent implements OnInit {
    @ViewChild('tacheField') tacheField: ElementRef;
    collaborateur: Collaborateur;
    errorMessageAction: string;
    actions: Array<Action>;
    activites: Array<Activite>;
    missions: Array<Mission> = new Array<Mission>();
    ActionStatut = ActionStatut;
    tache: Tache = new Tache();
    editModeTache: boolean = false;
    private documentSyncEmitter: ISubscription;
    fromDate: moment.Moment = moment().add(-5, 'weeks');
    pattern = /\n/g;

    constructor(private eventService: EventService, private pouchdbservice: PouchdbService, private authService: AuthService) {}

    ngOnDestroy() {
        if (this.documentSyncEmitter) {
            this.documentSyncEmitter.unsubscribe();
        }
    }

    ngOnInit() {
        this.loadData();
        this.documentSyncEmitter = this.eventService.getDocumentSyncEmitter().subscribe((item: DocumentSyncEvent) => {
            if (item.type === "Action") {
                this.loadActions();
            } else if (item.type === EventService.ALL) {
                this.loadActions();
                this.loadActivite();
                this.loadTodo();
            }
        });
    }


    loadData() {
        this.authService.collaborateurObs.subscribe(collaborateur => {
            this.collaborateur = collaborateur;
            this.loadActions();
            this.loadActivite();
            this.loadTodo();
        });

    }

    loadActivite() {
        if (this.collaborateur && this.collaborateur._id) {

            this.pouchdbservice.getActiviteForCollaborateurFromDate(this.collaborateur._id, getActiviteDateOfWeek(this.fromDate)).subscribe(
                datas => {
                    if (datas) {
                        let tmpMissions = new Array();
                        let missionId = new Array<string>();
                        datas.sort((d1, d2) => d2.date.localeCompare(d1.date)).forEach(activite => {
                            activite.imputations.forEach(imputation => {
                                if (imputation.mission && missionId.indexOf(imputation.mission._id) == -1) {
                                    let mission = new Mission();
                                    mission._id = imputation.mission._id;
                                    mission.nom = imputation.mission.nom;
                                    mission.numFacture = imputation.mission.nomCourt;
                                    tmpMissions.push(mission);
                                    missionId.push(imputation.mission._id);
                                }
                            })
                        });
                        this.activites = datas;
                        this.missions = tmpMissions;
                    } else {
                        
                        this.activites = [];
                        this.missions = [];
                    }
                },
                err => {
                    console.log(err);
                }
            );
        }
    }

    loadActions() {
        if (this.collaborateur && this.collaborateur._id) {
            this.pouchdbservice.getActionsForCollaborateur(this.collaborateur._id).subscribe(
                data => {
                    this.actions = data;
                },
                err => {
                    this.errorMessageAction = err;
                }
            );
        }
    }

    loadTodo() {
        if (this.collaborateur && this.collaborateur._id) {
            this.pouchdbservice.getTacheForCollaborateur(this.collaborateur._id).subscribe(
                data => {
                    if (data) {
                        this.tache = data;
                    }
                },
                err => {
                    console.log(err);
                }
            );
        }
    }


    changeAction(actionId: string, check: boolean) {
        for (let action of this.actions) {
            if (action._id === actionId) {
                action.statut = check ? ActionStatut.DONE : ActionStatut.TODO;
                this.pouchdbservice.save(action).subscribe(
                    (data) => {
                        action._rev = data.rev;
                    },
                    err => {
                        this.errorMessageAction = err;
                    }
                );
            }
        }
    }

    editTache(mode: boolean) {
        this.editModeTache = mode;
        if (mode) {
            setTimeout(() => this.tacheField.nativeElement.focus(), 0);
            ;
        }
    }

    saveTache() {
        if (!this.tache.collaborateurId) {
            this.tache.collaborateurId = this.collaborateur._id;
        }
        this.pouchdbservice.save(this.tache).subscribe(
            (data) => {
                this.tache._rev = data.rev;
                this.tache._id = data.id;
                this.editModeTache = false;
            },
            err => {
                this.errorMessageAction = err;
            }
        );
    }

}



