import {Component, OnInit, ViewChild} from '@angular/core';
import {ISubscription} from "rxjs/Subscription";
import {Parametres, Item, DocumentSyncEvent} from '../../model';
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import * as XLSX from 'xlsx';
import * as alertify from 'alertifyjs/build/alertify.js';
import {EventService} from '../../services/event-service/event.service';

@Component({
    selector: 'app-parametres',
    templateUrl: './parametres.component.html',
    styleUrls: ['./parametres.component.scss']
})
export class ParametresComponent implements OnInit {

    private documentSyncEmitter: ISubscription;
    @ViewChild('fileInput') fileInput;
    @ViewChild('fileInputCDT') fileInputCDT;
    editForm = false;
    parametres: Parametres = new Parametres();
    listesDeChoix: Array<string> = [];
    editParameterName: string;
    errorMessage: String;
    errorImportMessage: String;

    constructor(private eventService: EventService, private pouchdbservice: PouchdbService) {

    }

    ngOnDestroy() {
        if (this.documentSyncEmitter) {
            this.documentSyncEmitter.unsubscribe();
        }
    }

    ngOnInit() {
        this.loadData();
        this.documentSyncEmitter = this.eventService.getDocumentSyncEmitter().subscribe((item: DocumentSyncEvent) => {
            if (item.type === "Parametres" || item.type === EventService.ALL) {
                this.loadData();
            }
        });
    }

    loadData() {
        this.editForm = false;
        this.pouchdbservice.getParametres().subscribe(
            response => {
                this.listesDeChoix = Object.keys(response.listesDeChoix).sort();
                this.parametres = response;
            },
            err => {
                this.errorMessage = err;
            }
        );

    }

    edit(key: string) {
        this.editForm = true;
        if (this.editForm) {
            this.editParameterName = key;
        }
    }

    cancel() {
        this.editForm = false;
        this.editParameterName = null;
    }

    importProgramme() {
        let fileBrowser = <HTMLInputElement> this.fileInput.nativeElement;
        if (fileBrowser.files && fileBrowser.files[0]) {
            let f: File = fileBrowser.files[0];
            let reader = new FileReader();
            let self = this;
            reader.onload = function (e) {
                /* read workbook */
                let target: any = e.target;
                let bstr: string = target.result;
                const wb: XLSX.WorkBook = XLSX.read(bstr, {type: 'binary'});

                /* grab first sheet */
                const wsname: string = wb.SheetNames[0];
                const ws: XLSX.WorkSheet = wb.Sheets[wsname];

                /* save data */
                let lines: Array<Array<string>> = XLSX.utils.sheet_to_json(ws, {header: 1, range: 1});
                let programmes = new Array<Item>();
                let workpackage: Item = new Item();
                for (let cols of lines) {
                    if (cols.length >= 2) {
                        if (cols[0]) {
                            workpackage = new Item();
                            workpackage.nom = cols[0];
                            workpackage.numero = cols[0].substring(0, cols[0].indexOf(" ")).replace("WP","");
                            workpackage.sousItems = new Array<Item>();
                            programmes.push(workpackage);
                        }
                        if (cols[1]) {
                            let sousItem: Item = new Item();
                            sousItem.nom = cols[1];
                            sousItem.numero = cols[1].substring(0, cols[1].indexOf(" "));
                            workpackage.sousItems.push(sousItem);
                            if (cols.length===3 && cols[2]){
                                sousItem.commentaire = cols[2].trim();
                            }
                        }
                    }
                }
                self.parametres.workpackages = programmes;
                self.pouchdbservice.save(self.parametres).subscribe(
                    response => {
                        console.log(response);
                        self.loadData();
                        alertify.success('Les programmes ont t imports avec succs');
                        window.scrollTo(0, 0);
                        self.fileInput.nativeElement.value = "";
                    },
                    err => {
                        self.errorImportMessage = err;
                    }
                );
            };
            reader.readAsBinaryString(f);

        }
        else {
            this.errorImportMessage = "Veuillez slectionner un fichier";
        }
    }
    
    importCDT() {
        let fileBrowser = <HTMLInputElement> this.fileInputCDT.nativeElement;
        if (fileBrowser.files && fileBrowser.files[0]) {
            let f: File = fileBrowser.files[0];
            let reader = new FileReader();
            let self = this;
            reader.onload = function (e) {
                /* read workbook */
                let target: any = e.target;
                let bstr: string = target.result;
                const wb: XLSX.WorkBook = XLSX.read(bstr, {type: 'binary'});

                /* grab first sheet */
                const wsname: string = wb.SheetNames[0];
                const ws: XLSX.WorkSheet = wb.Sheets[wsname];

                /* save data */
                let colslines: Array<Array<string>> = XLSX.utils.sheet_to_json(ws, {header: 1, range: 1});
                let cdt = new Array<Item>();
                let item: Item = new Item();
                for (let cols of colslines) {
                    if (cols.length >= 2) {
                        if (cols[0]) {
                            item = new Item();
                            item.nom = cols[0];
                            item.numero = cols[0].substring(0, cols[0].indexOf(" ")).replace("CDT","");
                            item.sousItems = new Array<Item>();
                            cdt.push(item);
                        }
                        if (cols[1]) {
                            let sousItem: Item = new Item();
                            sousItem.nom = cols[1];
                            sousItem.numero = cols[1].substring(0, cols[1].indexOf(" "));
                            item.sousItems.push(sousItem);
                            if (cols.length===3 && !!cols[2]){
                                sousItem.commentaire = cols[2].trim();
                            }
                        }
                    }
                }
                self.parametres.cdt = cdt;
                self.pouchdbservice.save(self.parametres).subscribe(
                    response => {
                        console.log(response);
                        self.loadData();
                        alertify.success('Le CDT a t import avec succs');
                        window.scrollTo(0, 0);
                        self.fileInputCDT.nativeElement.value = "";
                    },
                    err => {
                        self.errorImportMessage = err;
                    }
                );
            };
            reader.readAsBinaryString(f);

        }
        else {
            this.errorImportMessage = "Veuillez slectionner un fichier";
        }
    }



}



