import {Observable} from 'rxjs/Observable';
import {Input, Output, Component, OnInit} from '@angular/core';
import {EventEmitter} from '@angular/core';
import {AuthService} from '../../services/auth/auth.service';
import {Collaborateur} from '../../model';

@Component({
    selector: 'app-menu',
    templateUrl: './menu.component.html',
    styleUrls: ['./menu.component.scss']
})
export class MenuComponent implements OnInit {

    @Input() menuDisplay: boolean;
    @Output() menuDisplayChange = new EventEmitter<boolean>();

    isAdmin$: Observable<boolean>;
    isLoggedIn$: Observable<boolean>;
    currentCollaborateur: Collaborateur = new Collaborateur();

    constructor(private authService: AuthService) {}

    ngOnInit() {
        this.isLoggedIn$ = this.authService.isLoggedIn;
        this.isAdmin$ = this.authService.isAdmin;
        this.authService.collaborateurObs.subscribe(collaborateur => {
            this.currentCollaborateur = collaborateur;
        });

    }

    onLogout() {
        this.closeMenu();
        this.authService.logout();
        return false;
    }
    
    changeMenuDisplay(){
        this.menuDisplay = !this.menuDisplay;
        this.menuDisplayChange.emit(this.menuDisplay)
    }
    
    closeMenu(){
        this.menuDisplay = false;
        this.menuDisplayChange.emit(this.menuDisplay)
    }
}


