import {Component, OnInit, OnDestroy, NgZone} from '@angular/core';
import {ISubscription} from "rxjs/Subscription";
import {DatePipe} from '@angular/common';
import {Note, Collaborateur, Adresse, Location, Parametres, Action, DocumentSyncEvent, Contact, Organisme} from '../../model';
import {FormGroup, FormBuilder, Validators, FormArray} from '@angular/forms';
import {ActivatedRoute} from "@angular/router";
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Router} from '@angular/router';
import {merge} from 'micro-dash';
import {} from 'googlemaps';
import {GooglePlaceService} from '../../services/google-place-service/google-place.service';
import {formatDateInString} from '../../utils';
import {PdfGeneratorService} from '../../services/pdf-generator/pdf-generator.service';
import * as alertify from 'alertifyjs/build/alertify.js';
import {environment} from '../../../environments/environment';
import {EventService} from '../../services/event-service/event.service';
import {AuthService} from '../../services/auth/auth.service';
import {ContactEditComponent} from '../../contact/edit/contact-edit.component';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';

@Component({
    selector: 'app-note',
    templateUrl: './note.component.html',
    styleUrls: ['./note.component.scss']
})
export class NoteComponent implements OnInit, OnDestroy {
    private documentSyncEmitter: ISubscription;
    dropbox_url = environment.dropbox_url;
    form: FormGroup;
    private formSubmitAttempt: boolean = false;
    errorMessage: string;
    editForm = false;
    createForm = false;
    note: Note = new Note();
    collaborateurs: Array<Collaborateur> = [];
    parametres: Parametres = new Parametres();
    private contacts: Array<Contact> = [];
    private organismes: Array<Organisme> = [];
    actionsForm: FormArray;
    actions: Array<Action> = [];
    private place: google.maps.places.PlaceResult
    currentCollaborateur: Collaborateur;

    constructor(private zone: NgZone, private eventService: EventService, private fb: FormBuilder, private route: ActivatedRoute, private pouchdbservice: PouchdbService,
        private router: Router, private datePipe: DatePipe, private pdfGeneratorService: PdfGeneratorService,
        private googlePlaceService: GooglePlaceService, private authService: AuthService,
        private modalService: NgbModal) {
        this.form = this.fb.group({
            noteType: ['', Validators.required],
            collaborateursConcernes: ['', Validators.required],
            date: ['', Validators.required],
            lieu: [''],
            adresse: this.fb.group({
                adresseComplete: '',
                complement: '',
                cedex: '',
                rue: '',
                numero: '',
                boitePostale: '',
                residence: '',
                ville: '',
                codePostal: '',
                location: this.fb.group({
                    latitude: '',
                    longitude: '',
                })
            }),
            objetDeclencheur: ['', Validators.required],
            faitObserve: ['', Validators.required],
            commentaire: [''],
            impactActivite: [''],
            contact: this.fb.group({
                _id: ['', Validators.required]
            }),
            organisme: this.fb.group({
                _id: ['']
            }),
            actions: this.fb.array([])
        });
        this.actionsForm = this.form.get('actions') as FormArray;
    }


    ngOnDestroy() {
        if (this.documentSyncEmitter) {
            this.documentSyncEmitter.unsubscribe();
        }
    }

    ngOnInit() {
        this.authService.collaborateurObs.subscribe(collaborateur => {
            this.currentCollaborateur = collaborateur;
        });
        this.route.params.subscribe(
            params => {
                var id = params['id'];
                if (id == '0') {
                    this.editForm = false;
                    this.createForm = true;
                } else {
                    this.loadNote(id);
                    this.documentSyncEmitter = this.eventService.getDocumentSyncEmitter().subscribe((item: DocumentSyncEvent) => {
                        if (item.type === "Note" && item.ids.indexOf(id) != -1) {
                            if (this.editForm) {
                                alertify.warning("La note a t modifie par un autre utilisateur, si vous enregistrez vos modifications, vous craserez les siennes")
                            }
                            this.loadNote(this.note._id);
                        }
                    });
                }
            });
        this.pouchdbservice.getCollaborateurs().subscribe(
            data => {
                this.collaborateurs = data;
            },
            err => {
                this.errorMessage = err;
            }
        );
        this.pouchdbservice.getOrganismes().subscribe(
            data => {
                this.organismes = data.sort((x, y) => x.nom.localeCompare(y.nom));
            },
            err => {
                console.log(err)
            }
        );
        this.pouchdbservice.getContacts().subscribe(
            data => {
                this.contacts = data.sort((x, y) => x.nom.localeCompare(y.nom));
            },
            err => {
                console.log(err)
            }
        );
        this.pouchdbservice.getParametres().subscribe(
            data => {
                this.parametres = data;
            },
            err => {
                this.errorMessage = err;
            }
        );
    }

    loadNote(id: string) {
        this.pouchdbservice.get(id).subscribe(
            data => {
                this.zone.run(() => {
                    this.note = data;
                });
            },
            err => {
                this.errorMessage = err;
            }
        );

        this.pouchdbservice.getActions(id, "Note").subscribe(
            data => {
                this.actions = data;
            },
            err => {
                this.errorMessage = err;
            }
        );
    }

    toggleEdit() {
        this.editForm = !this.editForm;
        if (this.editForm) {
            this.form.reset();
            this.actionsForm.controls = [];
            if (this.actionsForm) {
                for (let lien of this.actions) {
                    this.actionsForm.push(this.createAction(lien));
                }
            }
            this.form.patchValue(this.note);
            // check deleted contact/organisme/collaborateur
            if (this.actions) {
                this.actions.forEach(lien => {
                    if (!this.collaborateurs.find(item => item._id === lien.collaborateur._id)) {
                        lien.collaborateur.isDeleted = true;
                        this.collaborateurs.push(lien.collaborateur);
                    }
                });
            }
            if (this.note.contact && !this.contacts.find(item => item._id === this.note.contact._id)) {
                this.note.contact.isDeleted = true;
                this.contacts.push(this.note.contact);
            }
            if (this.note.organisme && !this.organismes.find(item => item._id === this.note.organisme._id)) {
                this.note.organisme.isDeleted = true;
                this.organismes.push(this.note.organisme);
            }
        }
    }

    isFieldInvalid(field: string) {
        return this.form.get(field).invalid && (this.form.get(field).touched || (this.form.get(field).untouched && this.formSubmitAttempt));
    }
    isFieldInvalidInActions(index: number, field: string) {
        let control = this.actionsForm.controls[index].get(field);
        return control.invalid && (control.touched || (control.untouched && this.formSubmitAttempt));
    }


    onSubmit() {
        this.errorMessage = "";
        if (this.form.valid && this.actionsForm.valid) {
            merge(this.note, this.form.getRawValue());
            if (!this.form.getRawValue()['actions'] || this.form.getRawValue()['actions'].length==0) {
                this.errorMessage = "Vous devez renseigner la suite  donner";
                return;
            }
            this.note['actions'] = undefined;
            if (!this.note._id && this.currentCollaborateur){
                this.note.auteur = new Collaborateur();
                this.note.auteur._id = this.currentCollaborateur._id;
                this.note.auteur.nom = this.currentCollaborateur.nom;
                this.note.auteur.prenom = this.currentCollaborateur.prenom;
            }
            if (this.note.collaborateursConcernes) {
                let collaborateursConcernes: Array<Collaborateur> = this.form.get('collaborateursConcernes').value;
                this.note.collaborateursConcernes = [];
                for (let coll of collaborateursConcernes) {
                    let newColl = new Collaborateur();
                    newColl._id = coll._id;
                    newColl.nom = coll.nom;
                    newColl.prenom = coll.prenom;
                    this.note.collaborateursConcernes.push(newColl);
                }
            }
            
            if (this.note.contact && this.note.contact._id) {
                let contact: Contact = this.contacts.find(org => org._id === this.note.contact._id);
                this.note.contact.nom = contact.nom;
                this.note.contact.prenom = contact.prenom;
            }
            if (this.note.organisme && this.note.organisme._id) {
                this.note.organisme.nom = this.organismes.find(org => org._id === this.note.organisme._id).nom;
            }

            this.saveNote(this.note);

        } else {
            this.errorMessage = "Tous les champs obligatoires ne sont pas renseigns";
        }
        this.formSubmitAttempt = true;
    }

    saveActions(noteId: string, actionsToSave: Array<Action>) {

        if (actionsToSave) {
            for (let action of actionsToSave) {
                action.entiteLienType = "Note";
                action.entiteLienId = noteId;
                action.entiteLienNom = this.note.noteType;
                if (action['id']) {
                    action._id = action['id'];
                    action['id'] = undefined;
                }
                if (action.collaborateur && action.collaborateur._id) {
                    let collaborateur = this.collaborateurs.find(org => org._id === action.collaborateur._id);
                    action.collaborateur.nom = collaborateur.nom;
                    action.collaborateur.prenom = collaborateur.prenom;
                }
            }
        }
        for (let actionToSave of actionsToSave) {
            var actionFound = this.actions.find(function (action) {
                return action._id === actionToSave._id;
            });
            if (actionFound) {
                actionFound = merge(actionFound, actionToSave);
                this.saveAction(actionFound);

            } else {
                let actionNew = new Action();
                actionNew = merge(actionNew, actionToSave);
                this.saveAction(actionNew);
            }
        }
        for (let actionOld of this.actions) {
            var actionFound = actionsToSave.find(function (action) {
                return action._id === actionOld._id;
            });
            if (!actionFound) {
                this.deleteAction(actionOld);
            }
        }
        this.swithToShowMode(noteId)
    }
    saveAction(action: Action) {
        this.pouchdbservice.save(action).subscribe(
            () => {},
            err => {
                this.errorMessage = err;
            }
        );
    }
    deleteAction(action: Action) {
        this.pouchdbservice.delete(action._id, action._rev).subscribe(
            () => {},
            err => {
                this.errorMessage = err;
            }
        );
    }



    saveNote(note: Note) {
        this.pouchdbservice.save(note).subscribe(
            response => {
                console.log(response);
                this.saveActions(response.id, this.actionsForm.value);
            },
            err => {
                this.errorMessage = err;
            }
        );
    }


    swithToShowMode(id) {
        if (this.createForm) {
            this.createForm = false;
            this.editForm = false;
            this.router.navigate(['/notes/' + id]);
        } else {
            this.createForm = false;
            this.editForm = false;
            this.loadNote(id);
        };
    }

    createAction(action: Action): FormGroup {
        return this.fb.group({
            id: [action._id],
            date: [action.date, Validators.required],
            typeAction: [action.typeAction, Validators.required],
            collaborateur: this.fb.group({
                _id: [action.collaborateur._id, Validators.required]
            })
        });
    }

    addAction() {
        this.actionsForm.push(this.createAction(new Action()));
    }

    removeAction(index: number) {
        this.actionsForm.removeAt(index);
    }

    compare(val1: Collaborateur, val2: Collaborateur) {
        if (!val1 || !val2) {
            return false;
        }
        return val1._id === val2._id;
    }

    openPdf() {
        this.pdfGeneratorService.generateNotePdfAndOpen(this.note);
    }

    setAddressOnChange(place: google.maps.places.PlaceResult) {
        this.place = place;
        if (this.place) {
            //set latitude, longitude and zoom
            var adresse: Adresse = new Adresse();
            adresse.location = new Location();
            adresse.location.latitude = this.googlePlaceService.latitude(this.place);
            adresse.location.longitude = this.googlePlaceService.longitude(this.place);
            adresse.numero = this.googlePlaceService.street_number(this.place);
            adresse.rue = this.googlePlaceService.street(this.place);
            adresse.codePostal = this.googlePlaceService.postal_code(this.place);
            adresse.ville = this.googlePlaceService.city(this.place);
            adresse.boitePostale = this.googlePlaceService.post_box(this.place);
            adresse.residence = this.googlePlaceService.establishment(this.place);
            adresse.complement = this.googlePlaceService.floor(this.place);
            adresse.ville = this.googlePlaceService.city(this.place);
            adresse['adresseComplete'] = null;
            this.form.get('adresse').patchValue(adresse);
        } else {
            this.form.get('adresse').get('location').setValue(new Location());
        }
    }

    addContact(event:Event) {
        event.preventDefault();
        const modalRef = this.modalService.open(ContactEditComponent, { size: 'lg' });
        modalRef.componentInstance.isModal = true;
        modalRef.result.then((_id) => {
            this.pouchdbservice.get(_id).subscribe(
                data => {
                    this.contacts.push(data);
                    this.contacts.sort((x, y) => x.nom.localeCompare(y.nom));
                    this.form.patchValue({contact: {_id: _id}});
                },
                err => {
                    console.log(err)
                }
        );
        }, (reason) => {
        });
    }




}


