import {Component, OnInit} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {FormGroup, FormBuilder, Validators, FormArray} from '@angular/forms';
import {Contact, Organisme, LienOrganisme} from '../../model';
import {ActivatedRoute} from "@angular/router";
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Router} from '@angular/router';
import {AuthService} from './../../services/auth/auth.service';
import * as alertify from 'alertifyjs/build/alertify.js';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';

@Component({
    selector: 'app-contact-edit',
    templateUrl: './contact-edit.component.html',
    styleUrls: ['./contact-edit.component.scss']
})
export class ContactEditComponent implements OnInit {
    form: FormGroup;
    private formSubmitAttempt: boolean = false;
    errorMessage: String;
    createForm = false;
    contact: Contact = new Contact();
    civilites: Array<string> = [];
    organismes: Array<Organisme> = [];
    isAdmin$: Observable<boolean>;
    liens: FormArray;
    isModal: boolean = false;

    constructor(public activeModal: NgbActiveModal, private fb: FormBuilder, private route: ActivatedRoute, private pouchdbservice: PouchdbService,
        private router: Router, private authService: AuthService) {
        this.isAdmin$ = this.authService.isAdmin;
        this.form = this.fb.group({
            nom: ['', Validators.required],
            prenom: ['', Validators.required],
            civilite: [''],
            telephone: [''],
            mail: [''],
            commentaire: [''],
            dateRencontre: [''],
            relationPrivilegiee: [''],
            liensOrganisme: this.fb.array([])
        });
        this.liens = this.form.get('liensOrganisme') as FormArray;
    }

    ngOnInit() {

        this.pouchdbservice.getOrganismes().subscribe(
            data => {
                this.organismes = data;
                this.route.params.subscribe(
                    params => {
                        var id = params['id'];
                        if (id == '0' || this.isModal || !id) {
                            this.createForm = true;
                        } else {
                            this.loadContact(id);
                        }
                    });
            },
            err => {
                this.errorMessage = err;
            }
        );
        this.pouchdbservice.getParametres().subscribe(
            data => {
                this.civilites = data && data.listesDeChoix && data.listesDeChoix.civilite;
            },
            err => {
                this.errorMessage = err;
            }
        );
    }

    loadContact(id: string) {
        this.pouchdbservice.get(id).subscribe(
            data => {
                this.contact = data;
                this.form.reset();
                this.liens.controls = [];
                if (this.contact.liensOrganisme) {
                    for (let lien of this.contact.liensOrganisme) {
                        this.liens.push(this.createLien(lien));
                    }
                    // check deleted organisme
                    this.contact.liensOrganisme.forEach(lien => {
                        if (!this.organismes.find(item => item._id === lien.organisme._id)) {
                            lien.organisme.isDeleted = true;
                            this.organismes.push(lien.organisme);
                        }
                    });
                }
                this.form.patchValue(this.contact);

            },
            err => {
                this.errorMessage = err;
            }
        );
    }


    isFieldInvalid(field: string) {
        return (
            this.form.get(field).invalid &&
            (this.form.get(field).touched || (this.form.get(field).untouched && this.formSubmitAttempt))
        );
    }

    onSubmit() {
        if (this.form.valid) {
            this.contact = Object.assign(this.contact, this.form.value);
            if (this.contact.liensOrganisme) {
                for (let lien of this.contact.liensOrganisme) {
                    if (lien.organisme && lien.organisme._id) {
                        lien.organisme.nom = this.organismes.find(org => org._id === lien.organisme._id).nom;
                    }
                }
            }
            this.save();
        }
        this.formSubmitAttempt = true;
    }

    save() {
        this.pouchdbservice.save(this.contact).subscribe(
            response => {
                console.log(response);
                this.swithToShowMode(response.id)
            },
            err => {
                this.errorMessage = err;
            }
        );
    }


    swithToShowMode(id) {
        if (this.isModal) {
            this.activeModal.close(id);
        } else {
            this.router.navigate(['/contacts/' + id]);
        }
    }

    display() {
        this.router.navigate(['/contacts/' + this.contact._id]);
    }

    close() {
        this.activeModal.dismiss();
    }

    createLien(lien: LienOrganisme): FormGroup {
        return this.fb.group({
            fonction: [lien.fonction],
            telephone: [lien.telephone],
            mail: [lien.mail],
            organisme: this.fb.group({
                _id: lien.organisme._id
            })
        });
    }

    addLien() {
        this.liens.push(this.createLien(new LienOrganisme()));
    }

    removeLien(index: number) {
        this.liens.removeAt(index);
    }

}



