import {Component, OnInit, ViewChild} from '@angular/core';
import {ISubscription} from "rxjs/Subscription";
import {Observable} from 'rxjs/Observable';
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Organisme, DocumentSyncEvent} from '../../model';
import {sanitize} from '../../utils';
import {Router} from '@angular/router';
import {EventService} from '../../services/event-service/event.service';
import {AuthService} from '../../services/auth/auth.service';
import {exportRowsToCsv} from '../../utils';


@Component({
    selector: 'app-organismes',
    templateUrl: './organismes.component.html',
    styleUrls: ['./organismes.component.scss']
})
export class OrganismesComponent implements OnInit {
    private documentSyncEmitter: ISubscription;
    rows: Array<Organisme> = [];
    datas: Array<Organisme> = [];
    isAdmin$: Observable<boolean>;
    loadingIndicator: boolean = true;
    activeFilter: boolean = true;
    texteFilter: string;
    selected = [];
    @ViewChild('table') table: any;

    columns: any[] = [
        {prop: 'nom', flexGrow: 1},
        {prop: 'typeOrganisme', name: 'Type', flexGrow: 2},
        {prop: 'adresse.ville', name: 'Ville', flexGrow: 1}
    ];

    constructor(private eventService: EventService, private pouchdbservice: PouchdbService, private authService: AuthService, private router: Router) {
        this.isAdmin$ = this.authService.isAdmin;
    }
    
    ngOnDestroy() {
        if (this.documentSyncEmitter) {
            this.documentSyncEmitter.unsubscribe();
        }
    }

    ngOnInit() {
        this.loadData();
        this.documentSyncEmitter = this.eventService.getDocumentSyncEmitter().subscribe((item: DocumentSyncEvent) => {
            if (item.type === "Organisme" || item.type === EventService.ALL) {
                this.loadData();
            }
        });
    }

    loadData() {
        this.pouchdbservice.getAllOrganismes().subscribe((data) => {
            this.datas = data;
            this.filter();
            setTimeout(() => {this.loadingIndicator = false;}, 1500);
        });
    }

    updateFilter(event) {
        let val = event.target.value;
        if (!val) {
            this.rows = this.datas;
            return;
        };
        this.texteFilter = sanitize(val);
        this.filter();
    }

    onSelect(event) {
        var selected: Array<Organisme> = event.selected;
        if (selected.length > 0) {
            this.router.navigate(['/organismes/' + selected[0]._id]);
        }
    }

    filterOnActif(value: boolean) {
        this.activeFilter = value;
        this.filter();
    }
    
    filter() {
        let self = this;
        const temp = this.datas.filter(function (d) {
            return (!self.texteFilter || sanitize(d.nom).indexOf(self.texteFilter) !== -1
                || (d.adresse && sanitize(d.adresse.ville).indexOf(self.texteFilter) !== -1)
                || sanitize(d.typeOrganisme).indexOf(self.texteFilter) !== -1)
                && (!self.activeFilter || !d.isDeleted);
        });
        this.rows = temp;
        // Whenever the filter changes, always go back to the first page
        this.table.offset = 0;
    }

    getRowClass(row: Organisme) {
        return {
            'deletedItem': row.isDeleted
        };
    }
    
    exportCsv() {
        var headerKeys = ["typeOrganisme","nom","adresse.numero","adresse.rue","adresse.complement","adresse.residence","adresse.boitePostale",
        "adresse.codePostal","adresse.ville","adresse.cedex","telephone","web"];
        exportRowsToCsv(headerKeys, this.rows, 'Export_prospection');
    }



}


