import {Component, OnInit, Input} from '@angular/core';
import {ImputationCube} from '../../../model';
import {nest, sum} from 'd3';

@Component({
    selector: 'app-chart-secteur-activite',
    templateUrl: './chart-secteur-activite.component.html',
    styleUrls: ['./chart-secteur-activite.component.scss']
})
export class ChartSecteurActiviteComponent implements OnInit {

    _datas: Array<ImputationCube> = [];

    @Input() set datas(values: Array<ImputationCube>) {
        this._datas = values;
        this.refreshData();
    }

    dataFiltered: any;

    constructor() {}

    ngOnInit() {
    }

    refreshData() {
        this.dataFiltered = nest()
            .key(function (d: ImputationCube) {return d.activite})
            .rollup(function (v) {return sum(v, function (d: ImputationCube) {return d.charge}) as any})
            .entries(this._datas)
            .map(function (group) {
                return {
                    name: group.key,
                    value: group.value
                };
            });

    }

}



