import {Component, OnInit, OnDestroy, NgZone} from '@angular/core';
import {ISubscription} from "rxjs/Subscription";
import {DatePipe} from '@angular/common';
import {Prospection, Collaborateur, Contact, Organisme, Parametres, Mission, Action, DocumentSyncEvent} from '../../model';
import {FormGroup, FormBuilder, Validators, FormArray} from '@angular/forms';
import {ActivatedRoute} from "@angular/router";
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Router} from '@angular/router';
import {merge} from 'micro-dash';
import {formatDateInString} from '../../utils';
import {PdfGeneratorService} from '../../services/pdf-generator/pdf-generator.service';
import * as alertify from 'alertifyjs/build/alertify.js';
import {environment} from '../../../environments/environment';
import {EventService} from '../../services/event-service/event.service';
import {AuthService} from './../../services/auth/auth.service';
import {ContactEditComponent} from '../../contact/edit/contact-edit.component';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';

@Component({
    selector: 'app-prospection',
    templateUrl: './prospection.component.html',
    styleUrls: ['./prospection.component.scss']
})
export class ProspectionComponent implements OnInit, OnDestroy {
    private documentSyncEmitter: ISubscription;
    dropbox_url = environment.dropbox_url;
    form: FormGroup;
    private formSubmitAttempt: boolean = false;
    errorMessage: string;
    editForm = false;
    createForm = false;
    prospection: Prospection = new Prospection();
    collaborateurs: Array<Collaborateur> = [];
    contacts: Array<Contact> = [];
    organismes: Array<Organisme> = [];
    parametres: Parametres = new Parametres();
    actionsForm: FormArray;
    actions: Array<Action> = [];
    currentCollaborateur: Collaborateur;

    constructor(private zone: NgZone, private eventService: EventService, private fb: FormBuilder, private route: ActivatedRoute, private pouchdbservice: PouchdbService,
        private router: Router, private datePipe: DatePipe, private pdfGeneratorService: PdfGeneratorService, private authService: AuthService,
        private modalService: NgbModal) {
        this.form = this.fb.group({
            nom: ['', Validators.required],
            contact: this.fb.group({
                _id: ['', Validators.required]
            }),
            client: this.fb.group({
                _id: ['']
            }),
            dateContact: ['', Validators.required],
            modaliteObtention: [''],
            cible: [''],
            thematique: [''],
            modaliteIS: [''],
            commentaire: [''],
            //            dropboxRepertoire: [''],
            montantProposition: [''],
            nbJours: [''],
            statut: ['', Validators.required],
            actions: this.fb.array([])
        });
        this.actionsForm = this.form.get('actions') as FormArray;
        this.authService.collaborateurObs.subscribe((collabo) => {
            this.currentCollaborateur = collabo;
        });
    }

    ngOnDestroy() {
        if (this.documentSyncEmitter) {
            this.documentSyncEmitter.unsubscribe();
        }
    }

    ngOnInit() {
        this.route.params.subscribe(
            params => {
                var id = params['id'];
                if (id == '0') {
                    this.editForm = false;
                    this.createForm = true;
                } else {
                    this.loadProspection(id);
                    this.documentSyncEmitter = this.eventService.getDocumentSyncEmitter().subscribe((item: DocumentSyncEvent) => {
                        if (item.type === "Prospection" && item.ids.indexOf(id) != -1) {
                            if (this.editForm) {
                                alertify.warning("La prospection a t modifie par un autre utilisateur, si vous enregistrez vos modifications, vous craserez les siennes")
                            }
                            this.loadProspection(this.prospection._id);
                        }
                    });
                }
            });
        this.pouchdbservice.getCollaborateurs().subscribe(
            data => {
                this.collaborateurs = data;
            },
            err => {
                this.errorMessage = err;
            }
        );
        this.pouchdbservice.getOrganismes().subscribe(
            data => {
                this.organismes = data.sort((x, y) => x.nom.localeCompare(y.nom));
            },
            err => {
                this.errorMessage = err;
            }
        );
        this.pouchdbservice.getContacts().subscribe(
            data => {
                this.contacts = data.sort((x, y) => x.nom.localeCompare(y.nom));
            },
            err => {
                this.errorMessage = err;
            }
        );
        this.pouchdbservice.getParametres().subscribe(
            data => {
                this.parametres = data;
            },
            err => {
                this.errorMessage = err;
            }
        );
    }

    loadProspection(id: string) {
        this.pouchdbservice.get(id).subscribe(
            data => {
                this.zone.run(() => {
                    this.prospection = data;
                });
            },
            err => {
                this.errorMessage = err;
            }
        );

        this.pouchdbservice.getActions(id, "Prospection").subscribe(
            data => {
                this.actions = data;
            },
            err => {
                this.errorMessage = err;
            }
        );
    }

    toggleEdit() {
        this.editForm = !this.editForm;
        if (this.editForm) {
            this.form.reset();
            this.actionsForm.controls = [];
            if (this.actionsForm) {
                for (let lien of this.actions) {
                    this.actionsForm.push(this.createAction(lien));
                }
            }
            this.form.patchValue(this.prospection);
            // check deleted contact/organisme/collaborateur
            if (this.actions) {
                this.actions.forEach(lien => {
                    if (!this.collaborateurs.find(item => item._id === lien.collaborateur._id)) {
                        lien.collaborateur.isDeleted = true;
                        this.collaborateurs.push(lien.collaborateur);
                    }
                });
            }
            if (this.prospection.contact && !this.contacts.find(item => item._id === this.prospection.contact._id)) {
                this.prospection.contact.isDeleted = true;
                this.contacts.push(this.prospection.contact);
            }
            if (this.prospection.client && !this.organismes.find(item => item._id === this.prospection.client._id)) {
                this.prospection.client.isDeleted = true;
                this.organismes.push(this.prospection.client);
            }
        }
    }

    isFieldInvalid(field: string) {
        return this.form.get(field).invalid && (this.form.get(field).touched || (this.form.get(field).untouched && this.formSubmitAttempt));
    }
    isFieldInvalidInActions(index: number, field: string) {
        let control = this.actionsForm.controls[index].get(field);
        return control.invalid && (control.touched || (control.untouched && this.formSubmitAttempt));
    }


    onSubmit() {
        if (this.form.valid && this.actionsForm.valid) {
            let oldStatut = this.prospection.statut;
            let updatedProspection = new Prospection();
            merge(updatedProspection, this.prospection);
            merge(updatedProspection, this.form.getRawValue());
            updatedProspection['actions'] = undefined;
            if (updatedProspection.contact && updatedProspection.contact._id) {
                let contact: Contact = this.contacts.find(org => org._id === updatedProspection.contact._id);
                updatedProspection.contact.nom = contact.nom;
                updatedProspection.contact.prenom = contact.prenom;
            }
            if (updatedProspection.client && updatedProspection.client._id) {
                updatedProspection.client.nom = this.organismes.find(org => org._id === updatedProspection.client._id).nom;
            }
            if (oldStatut !== updatedProspection.statut) {
                updatedProspection.dateChangementStatut = new Date();
                if (updatedProspection.statut.toUpperCase() === "invalide".toUpperCase() || updatedProspection.statut.toUpperCase() === "sans suite".toUpperCase()) {
                    updatedProspection.dateCloture = formatDateInString(new Date());
                }
                else if (updatedProspection.statut.toUpperCase() === "valide".toUpperCase()) {
                    updatedProspection.dateCloture = formatDateInString(new Date());
                    // check if already mission created for this prospection
                    this.pouchdbservice.getMissionFormProspectionId(this.prospection._id).subscribe(
                        data => {
                            let self = this;
                            if (!data || data.length == 0) {
                                alertify.confirm("Passer la prospection au statut 'Valide' va entrainer la cration d'une mission. Voulez-vous continuer?",
                                    function () {
                                        self.createMission(updatedProspection);
                                        self.saveProspection(updatedProspection);
                                    });
                            } else {
                                this.saveProspection(updatedProspection);
                            }
                        }
                    );
                    return;
                } else {
                    updatedProspection.dateCloture = null;
                }

            }
            this.saveProspection(updatedProspection);

        }
        this.formSubmitAttempt = true;
    }

    saveActions(prospectionId: string, actionsToSave: Array<Action>) {

        if (actionsToSave) {
            for (let action of actionsToSave) {
                action.entiteLienType = "Prospection";
                action.entiteLienId = prospectionId;
                action.entiteLienNom = this.prospection.nom;
                if (action['id']) {
                    action._id = action['id'];
                    action['id'] = undefined;
                }
                if (action.collaborateur && action.collaborateur._id) {
                    let collaborateur = this.collaborateurs.find(org => org._id === action.collaborateur._id);
                    action.collaborateur.nom = collaborateur.nom;
                    action.collaborateur.prenom = collaborateur.prenom;
                }
            }
        }
        for (let actionToSave of actionsToSave) {
            var actionFound = this.actions.find(function (action) {
                return action._id === actionToSave._id;
            });
            if (actionFound) {
                actionFound = merge(actionFound, actionToSave);
                this.saveAction(actionFound);

            } else {
                let actionNew = new Action();
                actionNew = merge(actionNew, actionToSave);
                this.saveAction(actionNew);
            }
        }
        for (let actionOld of this.actions) {
            var actionFound = actionsToSave.find(function (action) {
                return action._id === actionOld._id;
            });
            if (!actionFound) {
                this.deleteAction(actionOld);
            }
        }
        this.swithToShowMode(prospectionId)
    }
    saveAction(action: Action) {
        this.pouchdbservice.save(action).subscribe(
            () => {},
            err => {
                this.errorMessage = err;
            }
        );
    }
    deleteAction(action: Action) {
        this.pouchdbservice.delete(action._id, action._rev).subscribe(
            () => {},
            err => {
                this.errorMessage = err;
            }
        );
    }

    createMission(prospection: Prospection) {
        let mission: Mission = new Mission();
        mission._id = null;
        mission._rev = null;
        mission.type = "Mission";
        mission.nom = "Mission cre  partir de la prospection : " + prospection.nom;
        mission.prospectionId = prospection._id;
        mission.contact = prospection.contact;
        mission.client = prospection.client;
        mission.cible = prospection.cible;
        mission.thematique = prospection.thematique;
        mission.modaliteIS = prospection.modaliteIS;
        mission.modaliteObtention = prospection.modaliteObtention;
        mission.montantProposition = prospection.montantProposition;
        mission.dateDebut = this.datePipe.transform(new Date(), 'yyyy-MM-dd');
        mission.addCP(this.currentCollaborateur);
        this.pouchdbservice.save(mission).subscribe(
            response => {
                console.log(response);
            },
            err => {
                this.errorMessage = err;
            }
        );
    }

    saveProspection(prospection: Prospection) {
        this.pouchdbservice.save(prospection).subscribe(
            response => {
                console.log(response);
                this.saveActions(response.id, this.actionsForm.value);
            },
            err => {
                this.errorMessage = err;
            }
        );
    }


    swithToShowMode(id) {
        if (this.createForm) {
            this.createForm = false;
            this.editForm = false;
            this.router.navigate(['/prospections/' + id]);
        } else {
            this.createForm = false;
            this.editForm = false;
            this.loadProspection(id);
        };
    }

    createAction(action: Action): FormGroup {
        return this.fb.group({
            id: [action._id],
            date: [action.date, Validators.required],
            typeAction: [action.typeAction, Validators.required],
            collaborateur: this.fb.group({
                _id: [action.collaborateur._id, Validators.required]
            })
        });
    }

    addAction() {
        this.actionsForm.push(this.createAction(new Action()));
    }

    removeAction(index: number) {
        this.actionsForm.removeAt(index);
    }
    
    duplicateAction(index: number) {
        let action = this.actionsForm.at(index).value as Action;
        let newAction = new Action();
        newAction.date = action.date;
        newAction.collaborateur = action.collaborateur;
        newAction.typeAction = action.typeAction;
        this.actionsForm.push(this.createAction(newAction));
    }

    addContact(event:Event) {
        event.preventDefault();
        const modalRef = this.modalService.open(ContactEditComponent, { size: 'lg' });
        modalRef.componentInstance.isModal = true;
        modalRef.result.then((_id) => {
            this.pouchdbservice.get(_id).subscribe(
                data => {
                    this.contacts.push(data);
                    this.contacts.sort((x, y) => x.nom.localeCompare(y.nom));
                    this.form.patchValue({contact: {_id: _id}});
                },
                err => {
                    console.log(err)
                }
        );
        }, (reason) => {
        });
    }



    openPdf() {
        this.pdfGeneratorService.generateProspectionPdfAndOpen(this.prospection);
    }


}



