    remoteCouchDBAddress: string = REMOTE_COUCH_DB_ADDRESS;

    // initiate adapter class and hook up the observables
    constructor(private eventService: EventService, private http: HttpClient) {
        this.eventService.getDocumentSyncEmitter().subscribe((item: DocumentSyncEvent) => {
            if (item.type === "Parametres") {
                this._parametres = null;
            }
        });

    }

    initConnexion(login: string, password: string) {
        this._pouchDbAdapter = new PouchDbAdapter(this.eventService, REMOTE_COUCH_DB_ADDRESS, login, password);
        this.syncStatus = this._pouchDbAdapter.syncStatus.asObservable();
        this.couchdbUp = this._pouchDbAdapter.couchDbUp.asObservable();
        this.initDb();
    }

    closeConnexion() {
        this._pouchDbAdapter.clear();
        this._pouchDbAdapter = null;
    }

    setCurrentCollaborateur(collabo: Collaborateur) {
        this.currentCollaborateur = collabo;
    }


    getCollaborateurs(): Observable<Array<Collaborateur>> {
        return this._pouchDbAdapter.getDocsFromSelector({
            selector: {type: "Collaborateur", isDeleted: false}
        });
    }

    getAllCollaborateurs(): Observable<Array<Collaborateur>> {
        return this._pouchDbAdapter.getDocsOfType("Collaborateur");
    }

    getMissions(): Observable<Array<Mission>> {
        return this._pouchDbAdapter.getDocsOfType("Mission");
    }

    getMissionFormProspectionId(prospectionId: string): Observable<Array<Mission>> {
        return this._pouchDbAdapter.getDocsFromSelector({
            selector: {type: "Mission", prospectionId: prospectionId}
        });
    }

    getMissionFromNumFacture(numFacture: string): Observable<Mission> {
        return new Observable<Mission>(observer => {
            this._pouchDbAdapter.getDocsFromSelector({
                selector: {type: "Mission", numFacture: numFacture},
                limit: 1
            }).subscribe(
                (data) => {
                    observer.next(data[0])
                },
                err => observer.error(err));
        });
    }

    getCollaborateurByLogin(login: string): Observable<Collaborateur> {
        return new Observable<Collaborateur>(observer => {
            this._pouchDbAdapter.getDocsFromSelectorRemote({
                selector: {type: "Collaborateur", mail: login},
                limit: 1
            }).subscribe(
                (data) => {
                    observer.next(data[0])
                },
                err => observer.error(err));
        });
    }

    getMissionImputations(missionId: string): Observable<Array<Activite>> {
        let reduceMap = function (doc: Activite, emit) {
            if (doc.type === "Activite" && doc.imputations) {
                doc.imputations.forEach(function (imputation) {
                    if (imputation.mission && imputation.mission._id===missionId) {
                        emit(imputation.mission._id);
                    }
                });
            }
        }

        return new Observable<Array<Activite>>(observer => {
            this._pouchDbAdapter.getDocFromQuery(reduceMap, missionId).subscribe(
                (data) => {
                    observer.next(data)
                },
                err => observer.error(err));
        });
    }

    getImputationCube(dateDebut: string, dateFin: string): Observable<Array<ImputationCube>> {
        let self = this;
        return new Observable<Array<ImputationCube>>(observer => {
            this._pouchDbAdapter.getDocsFromSelector({
                selector: {type: "Activite", date: {$gte: dateDebut, $lte: dateFin}}
            }).subscribe(
                (data: Array<Activite>) => {
                    let result: Array<ImputationCube> = new Array<ImputationCube>();
                    data.forEach(activite => {
                        activite.imputations.forEach(function (imputation) {
                            let date = moment(activite.date);
                            imputation.charges.forEach(function (charge, i) {
                                if (charge.value > 0) {
                                    result.push(self.createImputationCube(activite, imputation, null, charge, date, i));
                                }
                                date.add(1, 'days');
                            });
                            imputation.sousImputations.forEach(function (sousImputation) {
                                let date = moment(activite.date);
                                sousImputation.charges.forEach(function (charge, i) {
                                    if (charge.value > 0 && imputation.charges[i].value > 0) {
                                        result.push(self.createImputationCube(activite, imputation, sousImputation, charge, date, i));
                                    }
                                    date.add(1, 'days');
                                });
                            });
                        });
                    });
                    observer.next(result);
                },
                err => observer.error(err));
        });
    }

    private createImputationCube(activite: Activite, imputation: Imputation, sousImputation: Imputation, charge: Charge, date: moment.Moment, index: number): ImputationCube {
        let impCube = new ImputationCube();
        impCube._id = activite._id;
        impCube.semaine = activite.semaine;
        impCube.collaborateurId = activite.collaborateur._id;
        impCube.collaborateurNom = activite.collaborateur.nom;
        impCube.mission = imputation.mission;
        impCube.contact = imputation.contact;
        impCube.organisme = imputation.organisme;
        //        if (imputation.entiteLienType === "Mission"){
        //            impCube.entiteLienAvancement = this.getMissionAvancement()
        //        }
        impCube.annee = date.year();
        impCube.date = formatMomentInString(date);
        impCube.mois = date.month();
        impCube.jour = date.date();
        if (sousImputation) {
            impCube.label = sousImputation.label;
            impCube.activite = sousImputation.activite;
            impCube.charge = (charge.value * imputation.charges[index].value) / 10000;
            impCube.categorieId = sousImputation.categorieId || "";
            impCube.categorieNom = sousImputation.categorieNom || "";
            impCube.sousCategorieId = sousImputation.sousCategorieId || "";
            impCube.sousCategorieNom = sousImputation.sousCategorieNom || "";
            impCube.description = sousImputation.description || "";
        } else {
            impCube.label = imputation.label;
            impCube.activite = imputation.activite;
            impCube.charge = charge.value / 100;
            impCube.categorieId = imputation.categorieId || "";
            impCube.categorieNom = imputation.categorieNom || "";
            impCube.sousCategorieId = imputation.sousCategorieId || "";
            impCube.sousCategorieNom = imputation.sousCategorieNom || "";
            impCube.description = imputation.description || "";
        }
        return impCube;
    }

