import {Component, OnInit, ViewChild} from '@angular/core';
import {ISubscription} from "rxjs/Subscription";
import {PouchdbService} from '../services/pouchdb-service/pouchdb.service';
import {EventService} from '../services/event-service/event.service';
import {ImputationCube, Collaborateur, Activite, DocumentSyncEvent} from '../model';
import {formatMomentInString, getActiviteDateOfWeek} from '../utils';
import * as alertify from 'alertifyjs/build/alertify.js';
import * as moment from 'moment';
import {exportRowsToCsv} from '../utils';

@Component({
    selector: 'app-pilotage',
    templateUrl: './pilotage.component.html',
    styleUrls: ['./pilotage.component.scss']
})
export class PilotageComponent implements OnInit {
    private documentSyncEmitter: ISubscription;

    datas: Array<ImputationCube> = [];
    rows: Array<ImputationCube> = [];

    dateDebut: string = formatMomentInString(moment().add(-1, 'month'));
    dateFin: string = formatMomentInString(moment());
    selectedCollaborateur: string = "";
    collaborateurs: Array<Collaborateur> = new Array<Collaborateur>();



    constructor(private eventService: EventService, private pouchdbservice: PouchdbService) {}
    
    ngOnDestroy() {
        if (this.documentSyncEmitter) {
            this.documentSyncEmitter.unsubscribe();
        }
    }

    ngOnInit() {
        this.loadData();
        this.documentSyncEmitter = this.eventService.getDocumentSyncEmitter().subscribe((item: DocumentSyncEvent) => {
            if (item.type === "Activite" || item.type === EventService.ALL) {
                this.loadData();
            }
        });
    }

    loadData() {
        this.changeDate();
        this.pouchdbservice.getCollaborateurs().subscribe(
            data => {
                this.collaborateurs = data;
            },
            err => {
                alertify.error(err);
            }
        );
    }

    changeDate() {
        if (this.dateFin < this.dateDebut) {
            this.dateFin = formatMomentInString(moment(this.dateDebut).endOf('month'));
        }
        this.pouchdbservice.getImputationCube(getActiviteDateOfWeek(moment(this.dateDebut)), this.dateFin).subscribe(
            data => {
                this.datas = data;
                this.rows = data;
                this.refreshData();
            },
            err => {
                alertify.error(err);
            }
        );
    }

    refreshData() {
        var self = this;
        this.rows = this.datas.filter(function (d: ImputationCube) {
            return (d.date >= self.dateDebut && d.date <= self.dateFin)
                && (self.selectedCollaborateur ? d.collaborateurId === self.selectedCollaborateur : true)
        });
    }

    exportCsv() {
        if (this.rows) {
            var headerKeys = [
                "annee",
                "semaine",
                "jour",
                "mois",
                "date",
                "collaborateurId",
                "collaborateurNom",
                "activite",
                "label",
                "categorieId",
                "categorieNom",
                "sousCategorieId",
                "sousCategorieNom",
                "description",
                "charge",
                "mission._id",
                "mission.nom",
                "mission.nomCourt",
                "contact._id",
                "contact.nom",
                "contact.nomCourt",
                "organisme._id",
                "organisme.nom",
                "organisme.nomCourt",
            ];

            exportRowsToCsv(headerKeys, this.rows, 'Export_pilotage_' + this.dateDebut + "_" + this.dateFin + (this.selectedCollaborateur ? "_" + this.selectedCollaborateur : ""));
        }
    }

}



