import {Component} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import * as alertify from 'alertifyjs/build/alertify.js';
import * as moment from 'moment';
import {AuthService} from './services/auth/auth.service';

@Component({
    selector: 'app-root',
    template: `
    <div [attr.id]="(isLoggedIn$ | async) ? 'wrapper': ''" [ngClass]="menuDisplay && 'showMenu'">
    <app-menu [(menuDisplay)]="menuDisplay"></app-menu>
    <div id="page-content-wrapper" class="container-fluid" (click)=closeMenu($event)>
        <router-outlet></router-outlet>
    </div>
    </div>
    <app-footer></app-footer>`,
    styleUrls: ['./app.component.scss']
})
export class AppComponent {
    isLoggedIn$: Observable<boolean>;
    menuDisplay: boolean = false;

    constructor(private authService: AuthService) {
        alertify.defaults.glossary.title = '';
        alertify.defaults.glossary.ok = 'OK';
        alertify.defaults.glossary.cancel = 'Annuler';
        alertify.defaults.transition = "slide";
        alertify.defaults.theme.ok = "btn btn-primary";
        alertify.defaults.theme.cancel = "btn btn-danger";
        alertify.defaults.theme.input = "form-control";
        moment.locale('fr');
    }

    ngOnInit() {
        this.isLoggedIn$ = this.authService.isLoggedIn;

    }
    
    closeMenu(event:Event){
        if (this.menuDisplay){
            this.menuDisplay = false;
            event.preventDefault();
            event.stopImmediatePropagation();
        }
    }
}



